import controller from './easeUiDropdownMenu.controller';
import 'ease-ui-dropmenu.scss';
export default {
    restrict: 'E',
    transclude: true,
    replace: true,
    bindings: {
        items: '=',
        select: '&',
        template: '=',
        isOpen: '=',
        componentId: '@',
        textOverflowEllipsis: '='
    },
    template: `
    <div data-ng-if="vm.template" class="ease-dropdown-container">
      <div class="ease-dropdown-background" data-ng-if = "vm.isOpen"></div>
      <ul data-ng-show = "vm.isOpen"
          class="ease-dropdown-list"
          id="{{vm.componentId}}-ease-ui-dropdown-menu"
          data-ng-transclude
          role="listbox"
          aria-expanded="{{vm.isOpen}}"
          >
      </ul>
    </div>
    <div data-ng-if="!vm.template" class="ease-dropdown-container">
    <div class="ease-dropdown-background" data-ng-if = "vm.isOpen"></div>
      <ul data-ng-show = "vm.isOpen"
          aria-label = "dropdown"
          class="ease-dropdown-list"
          id="{{vm.componentId}}-ease-ui-dropdown-menu"
          role="listbox"
          aria-expanded="{{vm.isOpen}}"
          >
          <li data-ng-repeat = "item in vm.items"
            data-index = {{$index}}
            role = "option"
            data-ng-if="!vm.template"
            id="{{vm.componentId}}-option-{{$index}}"
            tabindex = "-1"
          >
            <span class="ease-dropdown-label">{{item}}</span>
          </li>
       </ul>
    </div>
</div>
`,
    controller,
    controllerAs: 'vm'
};



