import * as angular from 'angular';
import easeSearchBarComponent from './easeSearchBar.component';
import easeSearchBarFilter from './easeSearchBar.filter';
/**
 * @ngdoc module
 * @name easeSearchBar
 * @module easeSearchBar
 * @description
 * Component that takes in a variable to store the text entered by the user (searchText) and a JSON object containing the static
 * content for the component that has either been fetched from Nuxeo or is customized by the user (staticContent) (see Language
 * Support section below). The text entered by the user can then be used as a parameter to filter on a list of transactions. This
 * component comes with a filter (easeSearchFilter) that can be used to filter transactions on any field, including date and monetary values
 * (please see below for an implementation example). This component will be used in conjunction with the easeFilterBar component to create
 * a full search and filter feature for transactions.
 *
 * Language Support
 *
 * Please note this section is only required for EASE consumers: all other consumers are not required to pass the
 * staticContent object (though they are welcome to use it to pass customized content to the component).
 * If the object is not passed, the component will fall back to default English content.
 *
 * The search bar component has been built to handle receiving static content from Nuxeo, in order to support multiple languages.
 * This is done via the staticContent parameter, which contains the placeholder and clearImgAlt keys that should have the static content values
 * fetched from Nuxeo. Currently, the available files are in Nuxeo under the EASE/CORE folder, and are named
 * 'core_component_transactionfilter_en_US.properties' (English) and 'core_component_transactionfilter_es_US.properties' (Spanish).
 * It is the consumer's responsibility to pass the correct content, as the component will simply display whatever is passed in the
 * staticContent object.
 *
 * @usage
 * ```html
 * <ease-search-bar search-text="searchTextVar" static-content="contentObj"></ease-search-bar>
 * ```
 *
 * @param {variable} searchText The variable used to store the search text entered by the user.
 * @param {string} [placeholder='Search Transactions'] Placeholder text displayed in the search bar.
 *
 * @example
 * <example name="ease-search-bar" module="searchBarExample">
 *   <file name="index.html">
 *     <script src="script.js"></script>
 *     <div ng-controller="SearchBarController">
 *       <ease-search-bar search-text="searchText" static-content="contentObj"></ease-search-bar>
 *       <p style="padding-left: 40px">
 *         <span class="cell">Date</span>
 *         <span class="cell">Merchant</span>
 *         <span class="cell">Category</span>
 *         <span class="cell">Amount</span>
 *       </p>
 *       <ul>
 *         <li data-ng-repeat="item in searchItems|easeSearchFilter:searchText:searchProperties">
 *           <p>
 *             <span class="cell">{{::item.date.toLocaleDateString()}}</span>
 *             <span class="cell">{{::item.merchant}}</span>
 *             <span class="cell">{{::item.category}}</span>
 *             <span class="cell">${{::item.amount}}</span>
 *           </p>
 *         </li>
 *       </ul>
 *     </div>
 *   </file>
 *   <file name="script.js">
 *     angular.module('searchBarExample', ['easeUIComponents'])
 *       .controller('SearchBarController', ['$scope', function($scope) {
 *         // This object should contain the content fetched from Nuxeo
 *         $scope.contentObj = {
 *           placeholder: 'Search Transactions',
 *           clearImgAlt: 'Clear Search'
 *         }
 *         $scope.searchText = ''
 *         $scope.searchItems = [
 *           {date: new Date('08/10/2016'), merchant: 'McDonalds', category: 'Dining', amount: '5.00'},
 *           {date: new Date('08/08/2016'), merchant: 'Old Navy', category: 'Merchandise', amount: '100.49'},
 *           {date: new Date('08/01/2016'), merchant: 'Sunoco', category: 'Automobile', amount: '30.00'},
 *           {date: new Date('07/23/2016'), merchant: 'Olive Garden', category: 'Dining', amount: '55.55'},
 *           {date: new Date('06/12/2016'), merchant: 'Hampton Inn', category: 'Hotels', amount: '229.00'},
 *           {date: new Date('05/20/2016'), merchant: 'American Eagle', category: 'Merchandise', amount: '108.00'},
 *           {date: new Date('04/21/2016'), merchant: 'Sakura', category: 'Dining', amount: '77.77'},
 *           {date: new Date('04/16/2016'), merchant: 'Hilton Garden Inn', category: 'Hotels', amount: '116.00'},
 *           {date: new Date('12/31/2016'), merchant: 'Exxon', category: 'Automobile', amount: '25.50'},
 *           {date: new Date('08/04/2015'), merchant: 'Taco Bell', category: 'Dining', amount: '6.52'}
 *         ]
 *         $scope.searchProperties = ['date', 'merchant', 'category', 'amount']
 *       }])
 *   </file>
 *   <file name="style.css">
 *     li {
 *       display: block;
 *     }
 *     p {
 *       border-bottom: 1px solid;
 *     }
 *     .cell {
 *       display: inline-block;
 *       width: 24%;
 *     }
 *   </file>
 * </example>
 */
export default angular
    .module('easeSearchBar', [])
    .component('easeSearchBar', easeSearchBarComponent)
    .filter('easeSearchFilter', easeSearchBarFilter);



