import { KEY } from '../../utilities/keyCode.util';
export default (function () {
    /**
     * @private
     * @function getInputWidth
     *
     * Get the width of the string.
     *
     * @param val Input field value
     * @returns {string} Pixel width of the given string
     */
    function getInputWidth(val) {
        return ((val.length + 1) * 8) + 10 + 'px';
    }
    /**
     * @private
     * @function textConcatScreenReader
     *
     * Concatenate the data point with the sublabe provided by user.
     *
     * @param secondaryData
     * @param tertiaryData
     *
     * @returns {string}
     */
    function textConcatScreenReader(secondaryData = '', tertiaryData = '', sublabel = '') {
        return `${secondaryData} ${sublabel} ${tertiaryData}`;
    }
    return class {
        /* @ngInject */
        constructor($scope, $filter, $element, $document, $timeout) {
            this.document = $document;
            this.element = $element;
            this.timeout = $timeout;
            this.items = this.persistItems;
            this.persistPlaceholder = this.combobox.placeholder;
            this.focus = false;
            this.required = false;
            this.message = '';
            this.index = 0;
            this.inputBox = this.element.eq(0).find('input')[0];
            this.arrow = require('arrow-down.svg');
            $scope.$watch(() => this.modelValue, (newVal) => {
                let filter = {};
                if (this.filterBy) {
                    filter[`${this.filterBy}`] = newVal;
                }
                else {
                    filter = newVal;
                }
                let menu = $filter('filter')(this.persistItems, filter);
                this.items = menu;
                if (this.modelValue)
                    this.inputBox.style.width = getInputWidth(newVal);
                else
                    this.inputBox.style.width = getInputWidth(this.combobox.placeholder);
            });
            $scope.$watch(() => this.required, (newVal) => {
                this.isRequired(newVal);
            });
            /**
             * Check if the user has selected the option from the menu when drop-down is closed.
             */
            $scope.$watch(() => this.isOpen, (newVal) => {
                if (!newVal) {
                    this.persistItems.length === this.items.length ? this.bgClickCloseDropdown() : null;
                    this.combobox.placeholder = this.modelValue || this.persistPlaceholder;
                }
            });
        }
        /**
         * @public
         * @function isRequired
         *
         * @param newVal value from user input box
         */
        isRequired(newVal) {
            if (newVal) {
                this.message = this.combobox.errorMessage;
            }
            else {
                this.message = '';
            }
        }
        /**
         * @public
         * @function toggleMenu
         *
         * @param evt
         */
        toggleMenu(evt) {
            if (evt.type === 'click') {
                this.openDropdown();
                this.timeout(() => this.element.find('input')[0].focus());
            }
        }
        /**
         * @public
         * @function openDropdown
         *
         * When drop down opens, make the current input box value become place holder
         */
        openDropdown() {
            this.isOpen = true;
            this.combobox.placeholder = this.modelValue || this.persistPlaceholder;
            this.items = this.persistItems;
            this.modelValue = '';
            this.selectedOptionInCombobox = '';
        }
        /**
         * @public
         * @function showSecondaryData
         *
         * show secondary data only when dropdown is closed or no value is in the input box
         */
        showSecondaryData() {
            return !this.isOpen || this.modelValue === '';
        }
        /**
         * @public
         * @function setData
         *
         * Extract selected list item index and bind it to view. Return the selected item to the user function.
         *
         * @param itemIndex of the selected item using key nav on combobox
         */
        setData(itemIndex) {
            let sampleListItem = this.persistItems[1];
            let item = this.persistItems[itemIndex];
            this.modelValue = typeof sampleListItem === 'object' && sampleListItem !== null ? this.persistItems[itemIndex][Object.keys(sampleListItem)[0]] : this.persistItems[itemIndex];
            this.combobox.placeholder = this.modelValue || this.persistPlaceholder;
            if (this.combobox.secondaryData) {
                this.combobox.secondaryData = this.persistItems[itemIndex][Object.keys(sampleListItem)[1]];
            }
            if (this.combobox.tertiaryData) {
                this.combobox.tertiaryData = this.persistItems[itemIndex][Object.keys(sampleListItem)[2]];
            }
            this.selectedOptionInCombobox = textConcatScreenReader(this.combobox.secondaryData, this.combobox.tertiaryData, this.combobox.sublabel);
            this.select({ item });
        }
        /**
         * @public
         * @function handleDownKey
         *
         * @param evt Keydown event
         */
        handleDownKey(evt) {
            if (evt.altKey)
                this.openDropdown();
            else if ((this.index < this.persistItems.length) && !this.isOpen) {
                this.setData(this.index);
                this.index++;
            }
            else {
                this.element.eq(0).find('LI')[0].focus();
                this.element.eq(0).find('LI').eq(0).addClass('active');
            }
        }
        /**
         * @public
         * @function handleUpKey
         *
         * @param evt Keydown event
         */
        handleUpKey(evt) {
            if (evt.altKey)
                this.isOpen = false;
            else if (this.index > 1 && !this.isOpen) {
                this.index--;
                this.setData(this.index - 1);
            }
        }
        /**
         * @public
         * @function keyDownInput
         *
         * @param evt Keydown event
         */
        keyDownInput(evt) {
            switch (evt.keyCode) {
                case KEY.ENTER:
                    evt.preventDefault();
                    if (!this.isOpen)
                        this.openDropdown();
                    break;
                case KEY.DOWN:
                    evt.preventDefault();
                    this.handleDownKey(evt);
                    break;
                case KEY.UP:
                    evt.preventDefault();
                    this.handleUpKey(evt);
                    break;
                case KEY.ESC:
                    if (this.isOpen) {
                        this.bgClickCloseDropdown();
                        evt.stopPropagation();
                        evt.preventDefault();
                    }
                    break;
                case KEY.TAB:
                    this.bgClickCloseDropdown();
                    evt.stopPropagation();
                    break;
                default:
                    if (!this.isOpen && !this.disableSearch && !evt.altKey)
                        this.isOpen = true;
            }
        }
        keyUpInput() {
            if (!this.disableSearch)
                this.isOpen = true;
        }
        /**
         * @public
         * @function bgClickCloseDropdown
         *
         * Close the drop-down menu while clicking outside.
         */
        bgClickCloseDropdown() {
            this.isOpen = false;
            if (this.combobox.placeholder !== this.persistPlaceholder)
                this.modelValue = this.combobox.placeholder;
            else
                this.modelValue = '';
        }
        /**
         * @public
         * @param val Boolean
         *
         * focus and blur the input box
         */
        isFocus(val) {
            this.focus = !val;
        }
    };
}());



