import controller from './easeCalendar.controller';
import templateUrl from './easeCalendar.tmpl.html';
import 'ease-calendar.scss';
/**
 * @ngdoc module
 * @name easeCalendar
 * @module easeCalendar
 *
 * @description
 * This component displays inline calendar. It takes a date and an object JSON containing options for your
 * calendar configuration. Please refer to the table below.
 * ease-calendar has momentJS as external dependency and for your i18n needs, please refer the http://momentjs.com/docs/#/i18n/changing-locale/ to configure momentjs.
 * @param {expression=} customClass function that helps you decorate any date in calendar month. Should return an object
 * that represents CSS class name related to current date and the label/text if any that you want to display below the date.
 * This function should return an object with foloowing json {cssClass: '', subLable: ''}.
 * @param {expression=} isDateDisabled function that gives you control over what to disable in given calendar month
 * @param {string=} calendar_format This follows momentjs format. Default is YYYY-MM-DD
 * @param {string=} format_day_title This format changes the calendar title that you want to display. Default is MMMM YYYY
 * @param {Date} min_date minimum available date in calendar. This has two way binding so if you update min_date, changes
 * should be reflected in calendar.
 * @param {Date} max_date maximum available date in calendar. You will not be able to navigate after that date.
 * @param {Date=} starting_date Use this property to programatically set the active/starting date in calendar.
 * @param {object=} staticContent Object used to extract your i18n string tokens. Must include properties such as
 * start_day_label, end_day_label, today_label
 * @usage
 * ```html
 * <ease-calendar
 *  ng-model="vm.dt"
 *  calendar-options="vm.inlineOptions"
 * />
 * ```
 *
 * customClass function gets the input object that holds a current date in month and boolean property selected. true if the
 * date is selected so that you can change the label on run time.
 * ```js
 * @param {Object} day object with date and weather it is selected or not
 * @return {Object} Object containing cssClass and subLabel property
 * function customClass(day) {
 *   var dayFromCalendar = new Date(day.date).setHours(0,0,0,0)
 *   var yourDueDate = new Date('July 22, 2016')
 *   if(dayFromCalendar == yourDueDate) {
 *     return {cssClass: 'due-day', subLabel: 'DUE' } //Label can be anything. Ideal try and have it i18n friendly
 *   } else {
 *     //Unfortunately you can not just ignore else portion. This is mandatory.
 *     return { cssClass: '', subLabel: '' }
 *   }
 * }
 *```
 * isDateDisabled has the same api except instead of getting an object, you only get date from calendar
 * ```js
 * // @param {Date} date object
 * // @return {boolean} true if you want to disable the given date, false otherwise.
 * function isDateDisable(date_) {
 *   // e.g you want to block July 24
 *   var dayToBlock = new Date('July 24, 2016').setHours(0,0,0,0)
 *   var currentDateFromCalendar = new Date(date_).setHours(0,0,0,0)
 *   //YOUR LOGIC FOR DISABLE DATES IN CALENDAR MONTH'
 *   if(dayToBlock == currentDateFromCalendar) {
 *     return true
 *   }
 * }
 *```
 *
 * @example
 * <example name="ease-calendar" module="easeCalendarExample">
 *   <file name="calendar.js">
 *       angular.module('easeCalendarExample', ['easeUIComponents'])
 *         .controller('CalendarDemoCtrl', function($scope) {
 *           var vm = this
 *           // Please DO IGNORE THIS FUNCTION. IT IS PURELY FOR DEMO
 *           function randomDate (start, end) {
 *             return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()))
 *           }
 *           // Default value of moment is ENGLISH.
 *           $scope.localeValue = 'en'
 *           vm.i18nData = {
 *             today_label: 'TODAY'
 *           }
 *           $scope.$watch('localeValue', function (newLocaleValue, oldLocaleValue) {
 *             $scope.localeValue = newLocaleValue || oldLocaleValue
 *             moment.locale($scope.localeValue)
 *             $scope.$applyAsync(function () {
 *               vm.dt = randomDate(new Date(2016, 0, 1), new Date(2018, 0, 1))
 *               vm.i18nData['today_label'] = getLabel('HOY', 'TODAY')
 *             })
 *           })
 *           this.dt = new Date()
 *           this.inlineOptions = {
 *             customClass: getDayClass,
 *             isDateDisabled: disableMe,
 *             format_day_title: 'MMMM YYYY',
 *             min_date: new Date('01/01/2016'),
 *             max_date: new Date('1/01/2018')
 *           }
 *           // [DEMO] Please ignore this piece of code. Purely for DEMONSTRATION.
 *           function getLabel (newLocaleValue, oldLocaleValue) {
 *             if ($scope.localeValue === 'es') {
 *               return newLocaleValue
 *             } else {
 *               return oldLocaleValue
 *             }
 *           }
 *           function getDayClass (data) {
 *             var date = data.date
 *             var dayToCheck = new Date(date).setHours(0, 0, 0, 0)
 *             var dueDay = new Date('July, 22, 2016').setHours(0,0,0,0)
 *             var lastDay = new Date('July 26, 2016').setHours(0,0,0,0)
 *
 *             if(data.selected) {
 *               if(dayToCheck > lastDay) {
 *                 return { cssClass: 'late-day', subLabel: getLabel('TARDE', 'LATE') }
 *               } else {
 *                 return { cssClass: '', subLabel: getLabel('PAGA', 'PAY') }
 *               }
 *             } else if(dayToCheck == dueDay) {
 *                 return { cssClass: 'due-day', subLabel: getLabel('DEBIDO', 'DUE') }
 *             } else {
 *                  return { cssClass: '', subLabel: '' }
 *             }
 *           }
 *           function disableMe (data) {
 *             var daysToDisabled = []
 *             var dayToCheck = new Date(data).setHours(0, 0, 0, 0)
 *             var daysInJanuary = [new Date('07, January 2017').setHours(0, 0, 0, 0), new Date('08, January 2017').setHours(0, 0, 0, 0)]
 *             var i = 0
 *             var date
 *             var current = new Date()
 *             while( i < 16) {
 *               date = new Date(current)
 *               daysToDisabled[i++] = date.setHours(0, 0, 0, 0)
 *               current.setDate(current.getDate() + 1)
 *             }
 *             if (daysToDisabled.indexOf(dayToCheck) !== -1 || daysInJanuary.indexOf(dayToCheck) !== -1) {
 *               return true
 *             }
 *           }
 *         })
 *   </file>
 *   <file name="index.html">
 *     <div ng-controller="CalendarDemoCtrl as vm" style="width: 300px; margin-left: 40%;">
 *       <pre>Selected date is: <em>{{vm.dt | date:'fullDate' }}</em></pre>
 *       <strong>*Please ignore changes in calendar when you click the checkbox. This is purely for demo.</strong>
 *       <label>Spanish:- <input
 *         type = "checkbox"
 *         ng-model= "localeValue"
 *         ng-true-value= "'es'"
 *         ng-false-value="'en'"
 *        ></label>
 *       <ease-calendar
 *        ng-model="vm.dt"
 *        calendar-options="vm.inlineOptions"
 *        static-content = "vm.i18nData"
 *       />
 *     </div>
 *   </file>
 * </example>
 */
export default () => {
    return {
        restrict: 'E',
        scope: {
            calendarOptions: '=',
            staticContent: '=?'
        },
        bindToController: true,
        templateUrl,
        require: ['easeCalendar', '^ngModel'],
        controller,
        controllerAs: 'vm'
    };
};



