import angular from 'angular';
import easeGoogleMapComponent from './easeGoogleMap.component';
/**
 * @ngdoc module
 * @name easeUiGoogleMap
 * @module easeUiGoogleMap
 * @description
 * Component that renders google map in any container. easeUiGoogleMap will trigger 'markerClick' event once the marker on the map is clicked.
 * Application developers can listen to this event in the parent controller to handle marker clicks. Parameters to the listener will be $event and marker object itself
 * easeGoogleMap accepts below mentioned parameners
 * An attribute to specify latitude (lat)
 * An attribute to specify longitude (long)
 * An attribute to specify the view, default is mapView(isStreetView)
 * An attribute to specify zoom level, default zoom level is 17 (zoom)
 * An attribute to mark multiple locations (markerPositions)
 * An attribute to enable/disable UI controlls on map (showUiControlls)
 * An attribute to display map as an image (staticMap)
 * An attribute to specify height and width of static map (size)
 * A  function pointer for map click handler  (clickHandler)
 * This can be used in conjunction with other components (i.e. Chrome modal).
 *
 * @usage
 * ```html
 * <ease-ui-google-map lat=38.8622732 long=-77.2318789 is-street-view=false zoom=8  marker-positions=[[38.19,-78.12],[38.831628,-77.2029567],[38.836289,-77.1929807]]
 * marker-messages=['Message_1','Message_2','Message_3']></ease-ui-google-map>
 * ```
 *
 * @param {number} lat Latitude (Required) .
 * @param {number} long Longitude (Required).
 * @param {boolean} is-street-view Should show map in Street View mode (Optional, Default is mapView).
 * @param {number} zoom Zoom level of the map (Optional, Default zoom level is 17).
 * @param {object} marker-config configuration for marker (Paramters lat (latitude) , long( longitude) , markerIcon{url :(Url for icon), x: (height) , y:(width)} ,markerText (Contents of marker window (String or html)) , id (id for marker) (Optional).
 * @param {boolean} show-ui-controls show/hide Ui controlls(Optional).
 * @param {boolen} static-map Flag to indicate if it is static map(Optional).
 * @param {string} size size of static map(Optional) Example (200 X 400).
 * @param {function} click-handler handler for map click(Optional).
 * @param {String} renderedMarkers If specified will provide array of all marker object rendered on map.
 * @param {String} image-description If specified will provide a meaningful description for static image describing map.
 *
 * @example
 * <example name="easeGoogleMapExample" module="easeGoogleMapExample">
 *   <file name="index.html">
 *     <div ng-controller="gmController as vm"  style="height: 430px;width:100%" >
 *         <span style="padding:400px;height:400px;width:100px;margin: 20px;"><input type="button" value="Alter Lat longs"  data-ng-click="handleBtnClick()"/></span>
 *        <ease-ui-google-map static-map=false lat='dynamicLat' long='dynamicLong' is-street-view=false zoom=8
 *         marker-config='dynamicMarkerPositions' click-handler="mapClickHandler()"></ease-ui-google-map>
 *
 *     </div>
 *   </file>
 *   <file name="script.js">
 *     angular.module('easeGoogleMapExample', ['easeUIComponents'])
 *    .controller('gmController', ['$scope', function($scope) {
 *             var contentString = '<div id="content">'+
 *                                       '<div id="siteNotice">'+
 *                                       '</div>'+
 *                                       '<h1 id="firstHeading" class="firstHeading">Uluru</h1>'+
 *                                       '<div id="bodyContent">'+
 *                                       '<p><b>Uluru</b>, also referred to as <b>Ayers Rock</b>, is a large ' +
 *                                       'sandstone rock formation in the southern part of the '+
 *                                       'Northern Territory, central Australia. It lies 335&#160;km (208&#160;mi) '+
 *                                       'south west of the nearest large town, Alice Springs; 450&#160;km '+
 *                                       '(280&#160;mi) by road. Kata Tjuta and Uluru are the two major '+
 *                                       'features of the Uluru - Kata Tjuta National Park. Uluru is '+
 *                                       'sacred to the Pitjantjatjara and Yankunytjatjara, the '+
 *                                       'Aboriginal people of the area. It has many springs, waterholes, '+
 *                                       'rock caves and ancient paintings. Uluru is listed as a World '+
 *                                       'Heritage Site.</p>'+
 *                                       '<p>Attribution: Uluru, <a href="https://en.wikipedia.org/w/index.php?title=Uluru&oldid=297882194">'+
 *                                       'https://en.wikipedia.org/w/index.php?title=Uluru</a> '+
 *                                       '(last visited June 22, 2009).</p>'+
 *                                       '</div>'+
 *                                   '</div>'
 *     $scope.mouseIn = function(marker,infoWindow) {
 *          infoWindow.open(marker.get('map'), marker)
 *       }
 *       $scope.mouseOut = function(marker,infoWindow) {
 *         infoWindow.close(marker.get('map'), marker)
 *       }
 *       var markerConfig1 = {lat : 38.19,long : -78.12,markerIconUrl : 'https://qa-content.kdc.capitalone.com/EASE/images/CORE/ATMFinder/MapMarker_ATM-Retail.png', markerText : contentString,id : '123',eventHandlers : [$scope.mouseIn,$scope.mouseOut],eventNames : ['mouseover','mouseout']}
 *       var markerConfig2 = {lat : 38.831628,long : -77.2029567,eventHandlers : [$scope.mouseIn,$scope.mouseOut],eventNames : ['mouseover','mouseout'], markerText : contentString}
 *       var markerConfig3 = {lat : 38.836289,long : -77.1929807, markerText : "Marker 3"}
 *
 *       $scope.dynamicMarkerPositions = [markerConfig1,markerConfig2,markerConfig3]
 *       $scope.dynamicLat=38.8622732
 *       $scope.dynamicLong=-77.2318789
 *
 *
 *       $scope.handleBtnClick = function () {
 *         console.log("calling callback")
 *         var markerConfig1 = {lat : -33.890542,long : 151.274856, markerText : contentString}
 *         var markerConfig2 = {lat : -33.923036,long : 151.259052, markerText : "Marker 2"}
 *         var markerConfig3 = {lat : -34.028249,long : 151.157507, markerText : "Marker 3"}
 *         $scope.dynamicMarkerPositions = [markerConfig1,markerConfig2,markerConfig3]
 *         $scope.dynamicLat=-37.92
 *         $scope.dynamicLong=151.25
 *         $scope.staticMap = true
 *       }
 *
 *
 *       $scope.$on("markerClick",function(event,marker){
 *         marker.setIcon("https://cdn3.iconfinder.com/data/icons/musthave/24/Stock%20Index%20Down.png")
 *       })
 *          }])
 *   </file>
 * </example>
 *
 */
export default angular.module('easeUiGoogleMap', [])
    .component('easeUiGoogleMap', easeGoogleMapComponent);



