class MonthPickerController {
    selectMonth(month) {
        if (month.available) {
            this.currentMonth = month;
        }
    }
    isAvailable(available) {
        if (available) {
            return 'available';
        }
        else {
            return 'not available';
        }
    }
    isActive(month, current) {
        return (month.month === current.month && month.year === current.year && month.date === current.date);
    }
}
export default MonthPickerController;



