import 'moment';
import * as angular from 'angular';
/**
 * @ngdoc module
 * @name easeCalendar(With Range)
 * @module easeRangeCalendar(With Range)
 *
 * @description
 * This component displays calendar with ability to select multiple dates. It takes an object JSON containing properties
 * for your calendar (same as base inline calendar). The only difference
 * to base calendar is the addition of an attribute 'multiple-selection' that adds the
 * ability to pass an array to calendar component to retrive user selected dates.
 * The first element of an array is your start and second is your end date. Remainer is the range between these two
 * dates.
 *
 * @usage
 * ```html
 * <ease-calendar
 *  ng-model="vm.dt"
 *  calendar-options="vm.inlineOptions"
 *  multiple-selection = "vm.selectedDates"
 * />
 * ```
 * @example
 * <example name="rangeCalendarDemo" module="rangeCalendarDemo">
 *   <file name="index.html">
 *     <div ng-controller="RangeCalendarCtrl as vm" style="width: 300px; margin-left: 40%;">
 *        <pre> Selected Dates:- {{vm.multipleSelection}} </pre>
 *        <h3> Start Date:- {{vm.startDate | date:'fullDate'}} </h3>
 *        <h3> End Date:- {{vm.endDate | date:'fullDate' }} </h3>
 *        <ease-calendar
 *          class = "calendar-container"
 *          ng-model="vm.dt"
 *          calendar-options="vm.inlineOptions"
 *          multiple-selection = "vm.multipleSelection"
 *        />
 *     </div>
 *   </file>
 *   <file name="rangeCalendarDemo.js">
 *     angular.module('rangeCalendarDemo', ['easeCalendar'])
 *       .controller('RangeCalendarCtrl', function($scope) {
 *          var vm = this
 *          this.dt = this.dt = null
 *          moment.locale('en')
 *          this.inlineOptions = {
 *            format_day_title: "MMMM, YYYY",
 *            min_date: new Date('01/01/2016'),
 *            max_date: new Date('12/12/2016')
 *          }
 *          this.multipleSelection = []
 *          this.startDate = null
 *          this.endDate = null
 *
 *          $scope.$watchCollection('vm.multipleSelection', function(newValue) {
 *            if(newValue.length > 1) {
 *              vm.startDate = new Date(newValue[0])
 *              vm.endDate = new Date(newValue[1])
 *            }
 *          })
 *        })
 *   </file>
 * </example>
 *
 */
/* @ngInject */
export default ($provide, $injector) => {
    const easeCalendarDelegate = ($delegate) => {
        let directive = $delegate[0];
        directive.compile = () => {
            return (scope, element, attrs, ctrls) => {
                if (!attrs.multipleSelection)
                    return;
                let calendarCtrl = ctrls[0];
                let ngModelCtrl = ctrls[1];
                calendarCtrl.selectedDates = [];
                scope.$parent.$watchCollection(attrs.multipleSelection, (newVal) => {
                    calendarCtrl.selectedDates = newVal;
                });
                ngModelCtrl.$viewChangeListeners.push(() => {
                    let newVal = scope.$parent.$eval(attrs.ngModel);
                    let dateVal = newVal.getTime();
                    let selectedDates = calendarCtrl.selectedDates;
                    if (!selectedDates.length || selectedDates.length > 1) {
                        return selectedDates.splice(0, selectedDates.length, dateVal);
                    }
                    selectedDates.push(dateVal);
                    let tempVal = Math.min.apply(null, selectedDates);
                    let maxVal = Math.max.apply(null, selectedDates);
                    tempVal = new Date(tempVal).setHours(24);
                    while (tempVal < maxVal) {
                        selectedDates.push(tempVal);
                        tempVal = new Date(tempVal).setHours(24);
                    }
                });
                scope.$watch(() => calendarCtrl.activeDate.getTime(), update);
                // [FIXME] [SD] This is as bad as it gets...
                scope.$watch(() => calendarCtrl.selectedDay, update);
                scope.$watch(calendarCtrl.selectedDates, update);
                function buildDayObject(current, start, day, end) {
                    const isStaticContent = angular.isUndefined(calendarCtrl.staticContent);
                    const startDayLabel = isStaticContent ? 'START' : calendarCtrl.staticContent['start_day_label'];
                    const endDayLabel = isStaticContent ? 'END' : calendarCtrl.staticContent['end_day_label'];
                    if (current.isSame(start) && current.isSame(end)) {
                        Object.assign(day, { uid: 'starting-day-range', subLabel: '\u00A0' });
                    }
                    else if (current.isSame(start)) {
                        Object.assign(day, { uid: 'starting-day-range', subLabel: startDayLabel });
                    }
                    else if (current.isSame(end)) {
                        Object.assign(day, { uid: 'ending-day-range', subLabel: endDayLabel });
                    }
                    else if (current.isBetween(start, end) || current.isBetween(end, start)) {
                        Object.assign(day, { uid: 'days-in-range', subLabel: '\u00A0' });
                    }
                    if (day['isSecondary']) {
                        Object.assign(day, { uid: `ease-calendar-${day.date.getMonth()}-${day.date.getDate()}`, subLabel: '' });
                    }
                    return day;
                }
                function update() {
                    calendarCtrl['date_rows'] = calendarCtrl['date_rows']
                        .map((week) => {
                        return week.map((day) => {
                            let start = moment(new Date(calendarCtrl.selectedDates[0]));
                            let end = moment(new Date(calendarCtrl.selectedDates[1]));
                            let current = moment(new Date(day['date']));
                            return buildDayObject(current, start, day, end);
                        });
                    });
                }
            };
        };
        return $delegate;
    };
    if ($injector.has('easeCalendarDirective')) {
        $provide.decorator('easeCalendarDirective', ['$delegate', easeCalendarDelegate]);
    }
};



