import 'ease-extensibility.scss';
import Controller from './easeExtensibilityBar.controller';
let easeExtensibilityBarComponent = {
    restrict: 'E',
    bindings: {
        content: '=',
        modalController: '@',
        redactEmptyCategories: '=',
        modalTemplateUrl: '@',
        mobileColumns: '=',
        modalTitle: '@',
        openModalCallBack: '&'
    },
    controller: ['$element', '$compile', '$scope', 'easeUIModalService', function (element, $compile, scope, easeUIModalService) {
            scope.$watch(() => this.content, (newVal, oldVal) => {
                scope.$applyAsync(() => {
                    this.content = newVal;
                    return performPreRendering(element, $compile, scope, easeUIModalService, this);
                }, true);
            });
            return performPreRendering(element, $compile, scope, easeUIModalService, this);
        }],
    controllerAs: 'vm'
};
const performPreRendering = (element, $compile, scope, easeUIModalService, config) => {
    let easeExtensibilityBarController = new Controller(config, easeUIModalService);
    element.html(easeExtensibilityBarController.html);
    let parentScope = scope.$parent;
    parentScope.vm = easeExtensibilityBarController;
    $compile(element.contents())(parentScope);
    return easeExtensibilityBarController;
};
export default easeExtensibilityBarComponent;



