const cropPubSub = () => {
    let events = {};
    return {
        evt: events,
        on(names, handler) {
            names.split(' ').forEach((name) => {
                if (!events[name]) {
                    events[name] = [];
                }
                events[name].push(handler);
            });
        },
        trigger(name, args) {
            events[name].forEach((handler) => {
                handler(args);
            });
        }
    };
};
export default cropPubSub;



