import angular from 'angular';
import easeInputComponent from './easeInput.component';
import easeInputConfig from './easeInput.conf';
/**
 * @ngdoc module
 * @name easeInput
 * @module easeFormInput
 * @description
 * Component containing a custom input box wrapped with a corresponding label, type, status, error messages, and content validation.
 * By default, the component takes in an id value for the input box (inputId), the variable for storing the input content (inputVal),
 * the label to display above the input box (label), and a JSON object containing the static content (placeholder, validationMsg,
 * maxCharMsg) for the component that has either been fetched from Nuxeo or is customized by the user (content). The consumer also
 * has the option to set the input box type to phone, email, or date (inputType). The consumer can also pass in a maximum character
 * limit (charLimit), the length, in characters, of the input box (length), a method to validate the content of the input box
 * (validateInput), and other parameters (see below documentation for more details). All the date error message should follow this object signature static-content = {dateValidationMsg: {invalidDate: '', invalidMaxDate:'', invalidMinDate:''}}
 * to prevent from crowding the component with attributes.
 *
 * FAQ
 *
 * Q: Why is icon in ease-input field not aligned right inside the input field and will it occur on EASE App after consumption?
 *
 * A: There is an inconsistency on the occurrence of this issue and it is only occurring on build book example.
 *    This issue wasn’t reproducible in EASE App during consumption testing. Development team is doing root cause analysis for this issue and will release a hot-fix based on the solution.
 *    In order to get the quickest help, please email #Houston directly.
 *
 * Q: Why does the error message not read out when using the JAWS screen reader?
 *
 * A: According to the Accessibility team, there is an issue with JAWS that prevents the error message from being announced
 *    to the user. As a result, we have added the aria-describedby attribute so that any present error message will be read out
 *    when the user is focused on the input field (as part of the description of the field).
 *
 * Language Support
 *
 * Please note this section is only required for EASE consumers: all other consumers are not required to pass the
 * staticContent object (though they are welcome to use it to pass customized content to the component).
 * If the object is not passed, the component will fall back to default English content.
 *
 * The input component has been built to handle receiving static content from Nuxeo, in order to support multiple languages.
 * This is done via the staticContent parameter, which contains the placeholder, validationMsg, and maxCharMsg keys. These keys
 * should have the static content values fetched from Nuxeo. Currently, the available files are in Nuxeo under the EASE/CORE folder,
 * and are named 'core_component_forminputs_en_US.properties' (English) and 'core_component_forminputs_es_US.properties' (Spanish).
 * It is the consumer's responsibility to pass the correct content, as the component will simply display whatever is passed in the
 * staticContent object.
 *
 * @usage
 * ```html
 * <ease-input static-content="contentObj" input-id="'yourId'" input-val="yourVariable" label="'Label Text'"
 *  input-type="'inputType'" input-status="'status'" char-limit="25" size="25" validate-input="validateMethod(content)"></ease-input>
 * ```
 *
 * @param {object} staticContent A JSON object containing the placeholder, validationMsg, dateValidationMsg and maxCharMsg properties (obtained from Nuxeo or customized). If no object is provided, the default English content from Nuxeo will be used. Please see the Language Support section above for more information.
 * @param {string} inputId The id value used to identify the input box. Note that this must be unique for the page where the component is being consumed.
 * @param {variable} inputVal The variable that will be used to store the contents of the input box. If you use the phone, please provide the initial value in this format (xxx) xxx-xxxx
 * @param {string} label The text label to be displayed above the input box.
 * @param {string} [placeholder=''] String to display if nothing has been entered in the input box. By default, nothing is displayed. For Date please provide MM/DD/YYYY or DD/MM/YYYY. It will be use for date validation and date conversion. NOTE: THIS PARAMETER IS DEPRECATED, PLEASE PASS THIS PARAMETER INSIDE OF THE staticContent OBJECT.
 * @param {string} [validationMsg=''] The customized error message to display to the user if the validation of the input fails. Date object signature {invalidDate: '', invalidMaxDate:'', invalidMinDate:''}. NOTE: THIS PARAMETER IS DEPRECATED, PLEASE PASS THIS PARAMETER INSIDE OF THE staticContent OBJECT.
 * @param {string} [maxCharMsg=''] Message to display when input reaches maximum character limit. NOTE: THIS PARAMETER IS DEPRECATED, PLEASE PASS THIS PARAMETER INSIDE OF THE staticContent OBJECT.
 * @param {integer} [charLimit=null] Max number of characters the user is allowed to enter in the input box. By default, there is no character limit.
 * @param {integer} [length=24] Length of input box, in number of characters.
 * @param {function} [validateInput=return true] The validation method that is run when the user leaves the input box. The method should take in the content of the input box as a parameter, and return true or false if the content passes or fails validation, respectively. By default, there is no validation on the content.
 * @param {string} [inputType=''] Input box type, current options are 'phone', 'email', 'date', or ''. Passing '' or not passing a value will result in a generic input box.
 * @param {string} [inputStatus=''] Input box status, locked or primary.
 * @param {Boolean} [isRequired='false'] This property sets the input to be required.
 * @param {string} [requiredMsg=''] The customized error message for when a required field is left empty after being touched.
 * @param {string} [errorStatus=''] External error status set by the user, value could be 'error' or ''. Consumer can pass in the status value to the input box to force error state.
 * @param {Boolean} [hasError='false'] Input error status. Consumer can watch this value to track the state of the input.
 * @param {string} [errorMsg=''] Input box error status message, consumer pass in the error message.
 * @param {string} [max=''] Input date max-date for validation (attribute needed if date validation is required).
 * @param {string} [min=''] Input date min-date for validation (attribute needed if date validation is required).
 * @param {string} [dateInfo='Today'] extra info for the date if its Today.
 * @param {function} [iconClick=''] Input date callback function for calendar click event.
 * @param {Boolean} [showIcon='false'] Input date show/hide calander icon. True will show the calander popup icon.
 *
 *
 * @example
 * <example name="ease-input" module="easeInputExample">
 *   <file name="index.html">
 *     <div ng-controller="InputExampleController">
 *       <ease-input static-content="contentObj" input-id="id" input-val="inputVal" input-status="{{status}}" label="label" char-limit="limit" length="size"
 *        validate-input="validate(content)" error-msg="errorMessage" error-status="errorStatus"></ease-input>
 *       <button data-ng-click="toggleLock()">Locked status</button>
 *       <button data-ng-click="togglePrimary()">Primary status</button>
 *       <button data-ng-click="toggleStatus()">Error Status</button>
 *       <button data-ng-click="reset()">Reset status</button>
 *     </div>
 *     <div ng-controller="emailInputController">
 *       <ease-input static-content="contentObj" input-id="id" input-val="inputVal" input-type = "{{inputType}}" input-status="{{status}}" label="label" char-limit="limit" length="size"></ease-input>
 *       <p>Current language is {{currentLang}}</p>
 *       <button data-ng-click="toggleLang()">Toggle Language</button>
 *     </div>
 *     <div ng-controller='DateInputController'>
 *       <p>Max date is today and min date is 30/11/2014</p>
 *       <ease-input
 *         static-content="contentObj"
 *         input-id='id'
 *         input-type='{{inputType}}'
 *         input-status='{{status}}'
 *         label='label'
 *         max='limit'
 *         min='minLimit'
 *         length='size'
 *         input-val = 'preDate'
 *         date-info = '{{dateInfo}}'
 *         icon-click = "lunchCalander()"
 *         show-icon = "showCal">
 *       </ease-input>
 *      <button ng-click="updateMaxDate()">Update max date</button>
 *       <ease-input
 *         static-content="contentObj1"
 *         input-id='id1'
 *         input-type='{{inputType1}}'
 *         input-status='{{status1}}'
 *         label='label1'
 *         max='limit1'
 *         length='size1'
 *         input-val = 'ngModel'
 *         date-info = '{{dateInfo1}}'
 *         icon-click = "lunchCalander1()">
 *       </ease-input>
 *     </div>
 *     <div ng-controller="phoneInputController">
 *       <ease-input
 *         static-content="contentObj"
 *         input-id="id"
 *         input-val="inputVal"
 *         input-type = "{{inputType}}"
 *         input-status="{{status}}"
 *         label="label"
 *         char-limit="limit"
 *         length="size">
 *       </ease-input>
 *       <p>Current language is {{currentLang}}</p>
 *       <button data-ng-click="toggleLang()">Toggle Language</button>
 *     </div>
 *     <div ng-controller="requiredInputController">
 *       <ease-input
 *       input-id="id"
 *       input-val="inputVal"
 *       input-status="{{status}}"
 *       label="label"
 *       char-limit="limit"
 *       length="size"
 *       is-required="isRequired"
 *       required-msg="requiredMessage"></ease-input>
 *     </div>
 *   </file>
 *   <file name="script.js">
 *     angular.module('easeInputExample', ['easeUIComponents'])
 *       .controller('InputExampleController', ['$scope', function ($scope) {
 *         $scope.contentObj = {
 *           placeholder: 'Placeholder text',
 *           validationMsg: 'Must be at least 5 characters'
 *         };
 *         $scope.inputVal;
 *         $scope.id = 'testInput';
 *         $scope.label = 'Basic Input';
 *         $scope.limit = 25;
 *         $scope.size = 35;
 *         $scope.errorStatus = '';
 *         $scope.status = '';
 *         $scope.errorMessage = 'API errors';
 *         $scope.togglePrimary = function () { $scope.status = 'primary';}
 *         $scope.toggleLock = function () { $scope.status = 'locked';}
 *         $scope.toggleStatus = function () { $scope.errorStatus = 'error';}
 *         $scope.reset = function () {
 *           $scope.status = '';
 *           $scope.errorStatus = '';
 *         }
 *         $scope.validate = function (content) {
 *           if (content.length < 5) {
 *             return false;
 *           }
 *           return true;
 *         }
 *       }])
 *       .controller('emailInputController', ['$scope', function ($scope) {
 *         // These content objects should contain the content obtained from Nuxeo
 *         var contentEnglish = {
 *           placeholder: 'yourname@email.com',
 *           validationMsg: 'This is not a valid email address.',
 *           maxCharMsg: 'Email must not exceed 50 characters.'
 *         };
 *         var contentSpanish = {
 *           placeholder: 'tunombre@ejemplo.com',
 *           validationMsg: 'Este no es un correo electrónico válido.',
 *           maxCharMsg: 'Correo electrónico no debe exceder 50 caracteres.'
 *         };
 *         $scope.contentObj = contentEnglish;
 *         $scope.inputVal;
 *         $scope.id = 'emailInput';
 *         $scope.inputType = 'email';
 *         $scope.label = 'Email';
 *         $scope.limit = 50;
 *         $scope.status = '';
 *         $scope.size = 60;
 *         $scope.currentLang = 'English';
 *         $scope.toggleLang = function () {
 *           if ($scope.currentLang === 'English') {
 *             $scope.currentLang = 'Spanish';
 *             $scope.contentObj = contentSpanish;
 *           } else {
 *             $scope.currentLang = 'English';
 *             $scope.contentObj = contentEnglish;
 *           }
 *         }
 *       }])
 *       .controller('DateInputController', ['$scope', function ($scope) {
 *         var dd = ['01', '02', '03', '04', '05', '06', '07', '08', '09'];
 *         var month = dd[(new Date().getMonth())] || new Date().getMonth() + 1;
 *         var day = dd[new Date().getDate()] || new Date().getDate();
 *         var today = [day, month, new Date().getFullYear()].join('/');
 *
 *         $scope.contentObj = {
 *           placeholder: 'DD/MM/YYYY',
 *           dateValidationMsg: {invalidDate: 'Format as DD/MM/YYYY.', invalidMaxDate: 'Max Date limit of ' + today + '.', invalidMinDate: 'Min Date limit of 30/11/2014.'}
 *         };
 *         $scope.id = 'easeDate';
 *         $scope.inputType = 'date';
 *         $scope.label = 'Birthday';
 *         $scope.limit = today;
 *         $scope.minLimit = '30/11/2014'
 *         $scope.status = 'date;'
 *         $scope.size = 10;
 *         $scope.preDate = '25/12/2014';
 *         $scope.dateInfo = 'Today';
 *         $scope.lunchCalander = function() { alert("You should have calendar here."); }
 *         $scope.id = 'easeDate';
 *         $scope.showCal = 'true';
 *         $scope.updateMaxDate = function() {
              $scope.limit = '01/10/2017'
              $scope.contentObj.dateValidationMsg.invalidMaxDate ='Max date limit has been updated to: ' + $scope.limit
            }
 *
 *         $scope.contentObj1 = {
 *           placeholder: 'MM/DD/YYYY',
 *           dateValidationMsg: {invalidDate: 'Format as MM/DD/YYYY.'}
 *         };
 *         $scope.id1 = 'easeDate1';
 *         $scope.inputType1 = 'date';
 *         $scope.label1 = 'Date Input';
 *         $scope.status1 = '';
 *         $scope.ngModel = '';
 *         $scope.dateInfo1 = 'Today';
 *         $scope.lunchCalander1 = function() { alert("You should have calendar1 toggle here."); }
 *
 *       }])
 *       .controller('phoneInputController', ['$scope', function ($scope) {
 *         // These content objects should contain the content obtained from Nuxeo
 *         var contentEnglish = {
 *           placeholder: '(XXX) XXX-XXXX',
 *           validationMsg: 'This is not a valid phone number.'
 *         };
 *         var contentSpanish = {
 *           placeholder: '(XXX) XXX-XXXX',
 *           validationMsg: 'Este no es un número de teléfono válido.'
 *         };
 *         $scope.contentObj = contentEnglish;
 *         $scope.inputVal = '(123) 456-7890';
 *         $scope.id = 'phoneInput';
 *         $scope.inputType = 'phone';
 *         $scope.label = 'Phone';
 *         $scope.limit = 14;
 *         $scope.status = '';
 *         $scope.size = 30;
 *         $scope.currentLang = 'English';
 *         $scope.toggleLang = function () {
 *           if ($scope.currentLang === 'English') {
 *             $scope.currentLang = 'Spanish';
 *             $scope.contentObj = contentSpanish;
 *           } else {
 *             $scope.currentLang = 'English';
 *             $scope.contentObj = contentEnglish;
 *           }
 *         }
 *       }])
 *      .controller('requiredInputController', ['$scope', function ($scope) {
 *         $scope.id = 'requiredInput';
 *         $scope.inputVal;
 *         $scope.limit = 14;
 *         $scope.label = 'Required Field';
 *         $scope.status = '';
 *         $scope.size = 30;
 *         $scope.isRequired = true;
 *         $scope.requiredMessage = 'This field is required';
 *       }])
 *   </file>
 * </example>
 */
export default angular.module('easeFormInput', [])
    .config(['$provide', '$injector', easeInputConfig])
    .directive('easeInput', [easeInputComponent]);



