import angular from 'angular';
import MonthPickerComponent from './easeMonthPicker.component';
/**
  * @ngdoc module
  * @name easeMonthPicker
  * @module easeMonthPicker
  *
  * @description
  * Component that takes in an array of months and outputs it as a grid where user can select one.  Only months that are available
  * will be clickable for the user, any unavailable months will be greyed out, and no action will occur if they
  * are clicked.  The component stores the selected month in a variable (currentMonth) that can be accessed and used.
  *
  * @usage
  * ```html
  * <ease-month-picker months="listOfMonths" current-month="someMonth"></ease-month-picker>
  * ```
  *
  * @param {array} months List of month objects that include name of month (month) and if month is available, stored as true/false (available).  Other properties, such as year and date (i.e. a statement ending date) can also be included in the object.
  * @param {object} currentMonth Variable that will be used to track the month selected by the user.
  *
  * @example
  * <example name="month-picker" module="monthPickerExample">
  *   <file name="index.html">
  *     <script src="script.js"></script>
  *     <div ng-controller="MonthPickerController" style="width: 240px; margin: 0 auto;">
  *       <ease-month-picker months="monthList" current-month="selectedMonth"></ease-month-picker>
  *       <p> Selected month is {{selectedMonth}} </p>
  *     </div>
  *   </file>
  *   <file name="script.js">
  *     angular.module('monthPickerExample', ['easeUIComponents'])
  *       .controller('MonthPickerController', ['$scope', function($scope) {
  *         $scope.monthList = [{month: 'Jan', date: '20', available: true}, {month: 'Feb', date: '20', available: true}, {month: 'Mar', date: '20', available: false}, {month: 'Apr', date: '20', available: true},
  *           {month: 'May', date: '20', available: false}, {month: 'Jun', date: '20', available: true}, {month: 'Jul', date: '20', available: false}, {month: 'Aug', date: '20', available: true},
  *           {month: 'Sep', date: '20', available: true}, {month: 'Oct', date: '20', available: false}, {month: 'Nov', date: '20', available: false}, {month: 'Dec', date: '20', available: true}]
  *         $scope.selectedMonth = {month: 'Apr', date: '20', available: true}
  *       }])
  *   </file>
  * </example>
*/
export default angular.module('easeMonthPicker', [])
    .component('easeMonthPicker', MonthPickerComponent);



