import controller from './easeSearchBar.controller';
import 'ease-search-bar.scss';
export default {
    restrict: 'E',
    bindings: {
        searchText: '=',
        staticContent: '=?'
    },
    template: `
    <div class="ease-search">
      <span class="ease-search-image" aria-hidden="true"><i><img src="{{vm.searchIcon}}"></i></span>
      <input
        class="ease-search-bar"
        id="easeSearchBar"
        aria-label="{{vm.staticContent.placeholder}}"
        type="text" role="search"
        placeholder="{{vm.staticContent.placeholder}}"
        data-ng-model="vm.searchText"
       >
      <button
        class="ease-search-clear"
        tabindex="0"
        data-ng-show="vm.searchText.length"
        data-ng-click="vm.clearText()">
        <i><img src="{{vm.clearIcon}}" alt="{{vm.staticContent.clearImgAlt}}"></i>
      </button>
    </div>`,
    controller,
    controllerAs: 'vm'
};



