import controller from './easeDrawer.controller';
import 'ease-drawer.scss';
export default {
    restrict: 'E',
    bindings: {
        drawerId: '=',
        maxHeight: '=?',
        isClickEnabled: '=?'
    },
    transclude: true,
    template: `<ul>
    <li
      id = {{::vm.drawerId}}
      tabindex = "-1"
      aria-expanded = "false"
      aria-hidden = "true"
      class = "ease-drawer-content"
      ng-transclude
    >
    </li>
  </ul>`,
    controller,
    controllerAs: 'vm'
};



