import { KEY } from 'keyCode.util';
import { PROPERTIES } from 'componentProperties.util';
const moment = require('moment');
const calIcon = require('icon-cal.svg');
const DATE_INPUT_LENGTH = 10;
const isToday = (date) => {
    let today = moment().startOf('day');
    return (today.diff(date) === 0);
};
/**
 * This returns the formatted date.
 * @param inputContent
 * @param inputVal
 * @param keyCode
 * @returns {string}
 */
const updatePostion = {
    onArraow: { 2: 3, 5: 6 },
    onDel: { 5: 4, 2: 1 },
    onNumberDel: { 5: 3, 2: 1 }
};
const formatDate = (inputContent, inputVal, keyCode) => {
    let cursorPos = inputContent.selectionStart || inputVal.length || 0;
    let numbers = inputContent.value.replace(/\D/g, '');
    let formatedDate = '';
    let dateFormatter = { 1: '/', 3: '/' };
    if (keyCode === KEY.FORWARDSLASH && !!(numbers.length % 2) && numbers.length < 5) {
        numbers = [numbers.slice(0, length - 1), '0', numbers.slice(length - 1)].join('');
    }
    if (keyCode === KEY.BACKSPACE && updatePostion.onDel[cursorPos]) {
        numbers = numbers.substr(0, updatePostion.onNumberDel[cursorPos]) + numbers.substr(updatePostion.onNumberDel[cursorPos] + 1);
        cursorPos = updatePostion.onDel[cursorPos];
    }
    for (let i = 0; i < numbers.length; i++) {
        formatedDate += numbers[i] + (dateFormatter[i] || '');
    }
    cursorPos = updatePostion.onArraow[cursorPos] || cursorPos;
    setTimeout(() => inputContent.setSelectionRange(cursorPos, cursorPos));
    return formatedDate;
};
const isValidDate = (userDate, ctrls, errMessage) => {
    if (!(userDate.isValid()) || ctrls.inputVal.length < DATE_INPUT_LENGTH) {
        ctrls.staticContent.validationMsg = errMessage.invalidDate || PROPERTIES.forminputs.date.mmddyyyy.error;
        return false;
    }
    return true;
};
const isBeforeMaxDate = (userDate, maxDate, ctrls, errMessage) => {
    if (userDate.isAfter(maxDate)) {
        ctrls.staticContent.validationMsg = errMessage.invalidMaxDate || `Date max limit of ${ctrls.max}.`;
        return false;
    }
    return true;
};
const isAfterMinDate = (userDate, minDate, ctrls, errMessage) => {
    if (userDate.isBefore(minDate)) {
        ctrls.staticContent.validationMsg = errMessage.invalidMinDate || `Date min limit of ${ctrls.min}.`;
        return false;
    }
    return true;
};
export default (ctrls) => {
    let keyCode, dateFormat;
    let errMessage = ctrls.staticContent.dateValidationMsg || ctrls.validationMsg;
    ctrls.staticContent.placeholder = ctrls.staticContent.placeholder || ctrls.placeholder || PROPERTIES.forminputs.date.mmddyyyy.hint;
    ctrls.charLimit = 10;
    dateFormat = ctrls.staticContent.placeholder.toUpperCase();
    ctrls.timeout(() => {
        if (ctrls.showIcon) {
            ctrls.displayIcon = calIcon;
            ctrls.iconAltText = 'Calendar Icon';
        }
    });
    ctrls.onInput = () => {
        ctrls.timeout(() => {
            let formateDate, content;
            content = ctrls.inputContent.value;
            ctrls.contentLength = content.length;
            formateDate = formatDate(ctrls.inputContent, ctrls.inputVal, keyCode);
            ctrls.inputContent.value = formateDate;
            ctrls.inputVal = formateDate;
        });
    };
    ctrls.onFocus = () => {
        let formateDate;
        ctrls.inFocus = false;
        ctrls.todayAlert = '';
        formateDate = formatDate(ctrls.inputContent, ctrls.inputVal, keyCode);
        ctrls.inputContent.value = formateDate;
        ctrls.inputVal = formateDate;
    };
    ctrls.onBlur = (e) => {
        let userDate = moment(ctrls.inputVal, dateFormat);
        if (ctrls.inputVal.length == 10 && userDate.isValid()) {
            ctrls.todayAlert = isToday(userDate) ? ctrls.dateInfo || 'Today' : '';
        }
        ctrls.checkContent(e);
    };
    ctrls.onKeydown = (e) => {
        keyCode = e.which;
        ctrls.handleKeydown(e);
    };
    ctrls.validateInput = (input) => {
        let userDate = moment(input.content, dateFormat);
        let maxDate = moment(ctrls.max, dateFormat);
        let minDate = moment(ctrls.min, dateFormat);
        return isValidDate(userDate, ctrls, errMessage) && isBeforeMaxDate(userDate, maxDate, ctrls, errMessage) && isAfterMinDate(userDate, minDate, ctrls, errMessage);
    };
    ctrls.hideDateInfo = () => {
        return { 'ease-date-info-hide': (ctrls.inputContent.offsetWidth < 200) };
    };
    ctrls.easeIconClick = (e) => {
        let iconSource = e.target.src;
        iconSource = iconSource.substring(iconSource.lastIndexOf(location.host) + location.host.length); //Creating absolute path removing any extra http://hostname or https://hostname
        if (calIcon === iconSource && ctrls.iconClick !== undefined)
            ctrls.iconClick();
    };
};



