import controller from './easeUiDropdown.controller';
import templateUrl from './easeUiDropdown.tmpl.html';
import 'ease-ui-dropdown.scss';
/* select, selectedItem are new DD */
let easeUiDropdownComponent = {
    restrict: 'E',
    transclude: true,
    replace: true,
    bindings: {
        persistItems: '=',
        items: '=',
        isOpen: '=',
        select: '&',
        modelValue: '=?',
        required: '=?',
        disableSearch: '=?',
        filterBy: '@?',
        combobox: '='
    },
    templateUrl,
    controller,
    controllerAs: 'vm'
};
export default easeUiDropdownComponent;



