import controller from './easeUIModal.controller';
import templateUrl from './easeUIModal.tmpl.html';
import 'ease-ui-modal.scss';
let easeModalComponent = {
    restrict: 'E',
    scope: {
        closeModal: '&',
        modalId: '=',
        modalTitle: '=',
        modalRole: '=?',
        doubleWide: '=?',
        fullScreen: '=?',
        showIconMobile: '=?'
    },
    bindToController: true,
    transclude: true,
    templateUrl,
    controller,
    controllerAs: 'vm'
};
export default () => {
    return easeModalComponent;
};



