import controller from './easeCalendarPopup.controller';
import templateUrl from './easeCalendar.popup.tmpl.html';
import 'ease-calendar-popup.scss';
export const easeCalendarPopup = () => {
    return {
        require: ['ngModel', 'easeCalendarPopup'],
        scope: {
            isOpen: '=?',
            calendarOptions: '=?'
        },
        bindToController: true,
        controller,
        controllerAs: 'vm'
    };
};
export const easeCalendarPopupWrap = () => {
    return {
        transclude: true,
        templateUrl
    };
};



