import * as angular from 'angular';
import { KEY } from '../../utilities/keyCode.util';
import { focusDropdownEntry } from '../../utilities/focusListEl.util';
let _timeout;
export default class {
    /* @ngInject */
    constructor($element, $document, $timeout, $scope) {
        _timeout = $timeout;
        this._document = $document;
        this._element = $element;
        this.selectedItem = '';
        this.selectedOption = null;
        $scope.$watch(() => this.isOpen, (val) => {
            this.toggleDropdown(val);
        });
    }
    set isOpenCtrl(val) {
        let index;
        let isDropdownMenuClicked = false;
        this.isOpen = val;
        this._element.on('keydown', (e) => {
            if (!this.keydown(e)) {
                e.stopPropagation();
                e.preventDefault();
            }
        });
        let clickHandler = (e) => {
            e.stopPropagation();
            isDropdownMenuClicked = this._element.eq(0)[0].contains(e.target);
            if (e.target.parentNode.dataset)
                index = e.target.dataset.index || e.target.parentNode.dataset.index;
            isDropdownMenuClicked ? this.selectItem(this.items[index]) : _timeout(() => { this.isOpen = false; });
        };
        if (val) {
            this.selectedOption = 0;
            this._element.on('click', clickHandler);
            angular.element(this.elems).removeClass('active');
        }
        else {
            this._element.off('click');
            this._element.off('keydown');
        }
        if (this.textOverflowEllipsis && val) {
            let label = this._element[0].getElementsByClassName('ease-itemlabel');
            angular.forEach(label, (val, key) => {
                label[key].style.textOverflow = 'ellipsis';
            });
        }
    }
    get isOpenCtrl() {
        return this.isOpen;
    }
    selectItem(item) {
        this.toggleDropdown(false);
        this.select({ item });
        this.selectedItem = item;
    }
    keydown(evt) {
        this.elems = this._element.find('li');
        switch (evt.keyCode) {
            case KEY.ESC:
                this.toggleDropdown(false);
                if (this._element.parent()[0].nodeName !== 'BODY')
                    this._element.parent().parent().find('input')[0].focus();
                break;
            case KEY.UP:
            case KEY.DOWN:
                if (this.isOpen)
                    this.selectedOption = focusDropdownEntry(evt.keyCode, this.elems, this.selectedOption);
                break;
            case KEY.ENTER:
                this.selectItem(this.items[evt.target.dataset.index]);
                if (this._element.parent()[0].nodeName !== 'BODY')
                    this._element.parent().parent().find('input')[0].focus();
                break;
            case KEY.TAB:
                this.selectItem(this.items[evt.target.dataset.index]);
                return true;
            case KEY.PAGEDOWN:
                focusDropdownEntry(evt.keyCode, this.elems, this.elems.length - 1);
                break;
            case KEY.PAGEUP:
                focusDropdownEntry(evt.keyCode, this.elems, 0);
                break;
        }
    }
    toggleDropdown(value) {
        _timeout(() => {
            this.isOpenCtrl = value;
        });
    }
}



