require('custom-event-polyfill/custom-event-polyfill');
class EaseModalController {
    /* @ngInject */
    constructor($document, $element, $timeout) {
        this.showIconMobile = this.showIconMobile || false;
        this.modalRole = this.modalRole || 'dialog';
        this.doubleWide = this.doubleWide || false;
        this.fullScreen = this.fullScreen || false;
        this.closeButton = require('close.svg');
        this.document = $document;
        this.element = $element;
        this.timeout = $timeout;
        this.body = this.document[0];
        this.modalBody = this.element[0].getElementsByClassName('ease-modal')[0];
        this.modalEnd = this.element[0].getElementsByClassName('ease-modal-end')[0];
        this.modalBody.focus();
        this.timeout(() => {
            this.closeElement = this.getCloseElement();
        });
        this.element.on('keydown', this.handleKeydown.bind(this));
    }
    close() {
        let ev = new CustomEvent('easeUiModalClose');
        dispatchEvent(ev);
        this.closeModal();
    }
    getCloseElement() {
        let el = this.element[0];
        let loopTargetID = el.getAttribute('loop-target');
        return loopTargetID ? el.querySelector(loopTargetID) : el.querySelector('.ease-modal-close-button');
    }
    handleKeydown(e) {
        // close modal if user hits escape key
        if (e.which === 27) {
            this.close();
            e.preventDefault();
        }
        // close modal when user is focused on close button and presses enter key
        if (e.which === 13 && this.closeElement === e.target) {
            this.close();
            e.preventDefault();
        }
        // keep focus in modal, go to close button when user tabs from last element
        if (e.which === 9 && !e.shiftKey) {
            if (this.modalEnd === e.target || this.modalBody === e.target) {
                e.preventDefault();
                this.getCloseElement().focus();
            }
        }
        // keep focus in modal, go to last element when user shift-tabs from close button
        if (e.which === 9 && e.shiftKey) {
            this.closeElement = this.getCloseElement();
            if (this.closeElement === e.target || this.modalBody === e.target) {
                e.preventDefault();
                this.modalEnd.focus();
            }
        }
    }
}
export default EaseModalController;



