import controller from './easeGoogleMap.controller';
let easeGoogleMapComponent = {
    restrict: 'E',
    bindToController: true,
    controller,
    bindings: {
        lat: '=',
        long: '=',
        zoom: '=',
        isStreetView: '=',
        markerConfig: '=',
        staticMap: '=',
        showUiControls: '=',
        lang: '@',
        size: '@',
        clickHandler: '&',
        renderedMarkers: '=',
        imageDescription: '@'
    },
    controllerAs: 'vm',
    template: '<ng-include src="vm.templateUrl">'
};
export default easeGoogleMapComponent;



