export default {
    keys: {
        13: 'enter',
        32: 'space',
        33: 'pageup',
        34: 'pagedown',
        35: 'end',
        36: 'home',
        37: 'left',
        38: 'up',
        39: 'right',
        40: 'down'
    },
    compare(date1, date2) {
        let _date1 = new Date(date1.getFullYear(), date1.getMonth(), date1.getDate());
        let _date2 = new Date(date2.getFullYear(), date2.getMonth(), date2.getDate());
        _date1.setFullYear(date1.getFullYear());
        _date2.setFullYear(date2.getFullYear());
        return _date1 - _date2;
    },
    split(arr, size) {
        let arrays = [];
        while (arr.length > 0) {
            let week = arr.splice(0, size);
            if (week.filter(({ isSecondary }) => isSecondary).length !== 7) {
                arrays.push(week);
            }
        }
        return arrays;
    },
    getAllDatesInMonth(startDate) {
        const numberDaysInMonth = 42;
        let dates = new Array(numberDaysInMonth);
        let current = new Date(startDate);
        let i = 0;
        let date;
        while (i < numberDaysInMonth) {
            //noinspection TypeScriptValidateTypes
            date = new Date(current);
            dates[i++] = date;
            current.setDate(current.getDate() + 1);
        }
        return dates;
    }
};



