import 'ease-content-tile.scss';
let easeContentTile = (transcludeFactory) => {
    return {
        restrict: 'E',
        scope: {
            tileId: '@',
            headerId: '@',
            firstId: '@',
            secondId: '@'
        },
        template: `
      <header data-ng-show="hasHeader"  class="header-tile" id = {{headerId}}> </header>
      <section class="content-tile" data-ng-class = {'withHeader':hasHeader} id = {{tileId}}>
      <aside class="heading-column" id = {{firstId}}></aside>
      <aside class="content-column" id = {{secondId}}></aside>
     </section>`,
        transclude: true,
        link(scope, elem, attr, ctrl, transcludeFn) {
            transcludeFactory.transclude(elem, transcludeFn);
            scope.hasHeader = transcludeFactory.getHeader();
        }
    };
};
easeContentTile.$inject = ['transcludeFactory'];
export default easeContentTile;



