import { KEY } from '../../utilities/keyCode.util';
export default class {
    /* @ngInject */
    constructor($scope, $filter, $element, $document, $timeout) {
        this.document = $document;
        this.element = $element;
        this.timeout = $timeout;
        this.modelValue = '';
        this.oldItems = this.items;
        this.arrow = require('arrow-down.svg');
        this.oldPlaceHolder = this.placeHolder;
        this.focus = false;
        this.required = false;
        this.message = '';
        $scope.$watch(() => { return this.modelValue; }, (newVal) => {
            let filter = {};
            if (this.filterKey) {
                filter[`${this.filterKey}`] = newVal;
            }
            else {
                filter = newVal;
            }
            let menu = $filter('filter')(this.oldItems, filter);
            this.items = menu;
            this.togglePlaceHolder = !!this.primaryData || !!this.secondaryData || this.modelValue;
        });
        $scope.$watch(() => { return this.isOpen; }, (newVal) => {
            if (!newVal) {
                this.element.find('div')[2].focus();
            }
        });
        $scope.$watch(() => { return this.required; }, (newVal) => {
            this.isRequired(newVal);
        });
    }
    isRequired(newVal) {
        if (newVal) {
            this.message = this.errorMessage;
        }
        else {
            this.message = '';
        }
    }
    toggleMenu(evt) {
        if (evt.type === 'click') {
            this.isOpen = true;
            this.clearSearchField();
            this.timeout(() => this.element.find('input')[0].focus());
        }
    }
    keydown(evt) {
        if (evt.keyCode === KEY.ENTER) {
            this.isOpen = true;
            this.clearSearchField();
        }
    }
    clearSearchField() {
        if (this.modelValue && (this.items).length)
            this.placeHolder = this.modelValue;
        this.items = this.oldItems;
        this.modelValue = '';
    }
    background() {
        this.isOpen = false;
        if (this.placeHolder !== this.oldPlaceHolder)
            this.modelValue = this.placeHolder;
    }
    isFocus(val) {
        this.focus = !val;
    }
}



