import controller from './easeInput.controller';
import templateUrl from './easeInput.tmpl.html';
import 'ease-input.scss';
export default () => {
    return {
        restrict: 'E',
        scope: {
            staticContent: '=?',
            inputId: '=',
            inputVal: '=',
            label: '=',
            placeholder: '=?',
            validationMsg: '=?',
            maxCharMsg: '=?',
            inputType: '@?',
            inputStatus: '@?',
            errorStatus: '=?',
            hasError: '=?',
            errorMsg: '=?',
            charLimit: '=?',
            length: '=?',
            validateInput: '&?',
            max: '=?',
            min: '=?',
            dateInfo: '@?',
            iconClick: '&?',
            showIcon: '=?',
            isRequired: '=?',
            requiredMsg: '=?'
        },
        bindToController: true,
        templateUrl,
        controller,
        controllerAs: 'vm'
    };
};



