import { positionFactory } from 'position.util';
import mustache from 'mustache';
import deviceDetector from 'devicedetector';
import partial from './easeTooltip.mustache';
export default (function () {
    /* Private helper methods */
    const BREAKPOINTS = ['desktop', 'tablet', 'phone'];
    const OPPOSITE_POSITIONS = { top: 'bottom',
        left: 'right',
        right: 'left',
        bottom: 'top' };
    const ALIGNMENT_MODIFIER = { top: 24,
        left: 24,
        right: 24,
        bottom: 16 };
    const CARAT_PADDING = 15;
    /**
     * Check params are not null and not undefined
     *
     * @private
     * @function checkParamValidity
     *
     * @param {list_of_args}  args - list of arguments
     * @returns {boolean}
     */
    const checkParamValidity = function (...args) {
        return args.every(param => !!param);
    };
    /**
     * Used to trigger mobile/tablet-only code
     *
     * @private
     * @function isNotDesktop
     *
     * @returns {boolean}
     */
    const isNotDesktop = function () {
        return deviceDetector.device !== 'desktop';
    };
    /**
     * Check if element is visible in window
     *
     * @private
     * @function isVisible
     *
     * @returns {boolean}
     * @param {HTMLElement} el - element to check
     */
    const isVisible = function (el) {
        return el.offsetParent !== null;
    };
    /**
     * If trying to use a position object with auto (not intended use),
     * just grab the first position value from the position object
     *
     * @private
     * @function getCleanedPosition
     *
     * @returns {string|object}
     * @param {boolean} positionAuto
     * @param {string|object} position
     */
    const getCleanedPosition = function (positionAuto, position) {
        let cleanPosition = position;
        if (positionAuto && typeof position !== 'string') {
            cleanPosition = position[Object.keys(position)[0]];
        }
        return cleanPosition;
    };
    /**
     * Parses the position string and generates the appropriate position classes.
     * If breakpt is present, modifies class name accordingly.
     *
     * @private
     * @function getPositionClasses
     *
     * @returns {array} - an array of position class strings for use with CSS for a given position
     * @param {string} position - note NOT object: difference from getAllPositionClasses
     * @param {string} breakpt  - optional
     */
    const getPositionClasses = function (position, breakpt) {
        let placementClasses = [];
        if (breakpt === undefined || (BREAKPOINTS.indexOf(breakpt) > -1)) {
            let [primaryPos, alignment] = position.split('-');
            let modifier = breakpt ? breakpt + '-' : '';
            placementClasses.push(`${modifier}position-${primaryPos}`);
            if (alignment) {
                placementClasses.push(`${modifier}position-align-${alignment}`);
            }
        }
        return placementClasses;
    };
    /**
     * If position object is passed in, attach for each breakpoint
     * Uses helper method getPositionClasses
     *
     * @private
     * @function getAllPositionClasses
     *
     * @returns {array} - (the complete) array of position class strings for use with CSS
     * @param {string|object} position
     */
    const getAllPositionClasses = function (position) {
        let positionClasses = ['position-top'];
        if (typeof position === 'string') {
            positionClasses = getPositionClasses(position);
        }
        else if (position !== null && typeof position === 'object') {
            Object.keys(position).forEach(breakpt => {
                positionClasses = positionClasses.concat(getPositionClasses(position[breakpt], breakpt));
            });
        }
        return positionClasses;
    };
    /**
     * Overwrites defaults and calcs/sets additional params for the component config
     *
     * @private
     * @function getConfig
     *
     * @returns {object}
     * @param {string}  tipMessage
     * @param {number}  id
     * @param {object}  options
     * @param {Window}  windowObj
     */
    const getConfig = function (tipMessage, id, options, windowObj) {
        // defaults
        let config = { attrs: [],
            position: 'top',
            positionAuto: false,
            showIcon: true,
            size: 'large',
            triggerMessage: '' };
        Object.assign(config, options);
        config.tipMessage = tipMessage;
        config.id = id;
        config.cleanedPosition = getCleanedPosition(config.positionAuto, config.position);
        config.curPosition = config.cleanedPosition;
        config.positionClasses = getAllPositionClasses(config.cleanedPosition);
        config.notDesktop = isNotDesktop();
        return config;
    };
    /* DOM-related helpers */
    /**
     * Used with bindMobileClickout--this is the bound function.
     * Since visibility is handled by a hidden checkbox toggle in mobile,
     * this function unchecks the box -> hides the tooltip.
     *
     * @private
     * @function getBoundFocusCallback
     *
     * @returns {function}  - a focus callback bound to the appropriate element
     * @param {HTMLElement} el  - should contain a checkbox
     */
    const getBoundFocusCallback = function (el) {
        let uncheckVisibleBox = function (el) {
            el.getElementsByClassName('check-visible')[0].checked = false;
        };
        return uncheckVisibleBox.bind(null, el);
    };
    /**
     * As the name indicates, this function is intended for mobile behavior:
     * when a user focuses out (taps elsewhere), the tooltip visibility should
     * be hidden (handled in the callback).
     * listenerArr, here and in following eventListener-related methods, is used
     * to keep track of what events are bound on the component
     *
     * @private
     * @function bindMobileClickout
     *
     * @param {HTMLElement} el
     * @param {function} focusCallback
     * @param {array} listenerArr
     */
    const bindMobileClickout = function (el, focusCallback, listenerArr) {
        el.addEventListener('focusout', focusCallback);
        listenerArr.push('mobileClickout');
    };
    /**
     * Initial autoPosition if currently visible, otherwise defer til visible
     * (attach a one-use listener)
     *
     * @private
     * @function autoPositionHandler
     *
     * @param {HTMLElement} el
     * @param {function} positioningCallback
     * @param {array} listenerArr
     */
    const autoPositionHandler = function (el, positioningCallback, listenerArr) {
        if (isVisible(el)) {
            positioningCallback();
        }
        else {
            let autoPositionListenerOnce = function () {
                positioningCallback();
                el.removeEventListener('mouseenter', autoPositionListenerOnce);
                listenerArr.splice(listenerArr.indexOf('deferredInitAutoPosition'), 1);
            };
            el.addEventListener('mouseenter', autoPositionListenerOnce);
            listenerArr.push('deferredInitAutoPosition');
        }
    };
    /**
     * Handles window resize activity with a series of one-use event listeners.
     * Adds initial listener, and then:
     * 1) on window resize event, attach the repositionListener once
     *    (ie flag that a reposition is needed) and then remove the window listener
     * 2) then on mouseenter of the tooltip, autoposition once,
     *    rebind the window listener once, and then remove the mouse listener
     * 3) go back to (1) if user resizes again in the future
     * So the user could in theory resize the window as much as they want;
     * as long as they don't mouseenter the tooltip, autoPosition doesn't get
     * called needlessly
     *
     * @private
     * @function bindWindowResize
     *
     * @param {HTMLElement} el
     * @param {function} positioningCallback
     * @param {Window}  window
     * @param {array} listenerArr
     */
    const bindWindowResize = function (el, positioningCallback, windowObj, listenerArr) {
        let repositionListenerOnce = function () {
            positioningCallback();
            el.removeEventListener('mouseenter', repositionListenerOnce);
            listenerArr.splice(listenerArr.indexOf('repositionOnce'), 1);
            windowObj.addEventListener('resize', addRepositionListenerOnce);
            listenerArr.push('windowResize');
        };
        let addRepositionListenerOnce = function () {
            el.addEventListener('mouseenter', repositionListenerOnce);
            listenerArr.push('repositionOnce');
            windowObj.removeEventListener('resize', addRepositionListenerOnce);
            listenerArr.splice(listenerArr.indexOf('windowResize'), 1);
        };
        windowObj.addEventListener('resize', addRepositionListenerOnce);
        listenerArr.push('windowResize');
    };
    /**
     * Triggers needed methods if positionAuto feature is true
     *
     * @private
     * @function bindAutoPositionEvents
     *
     * @param {HTMLElement} el
     * @param {function} positioningCallback
     * @param {Window}  windowObj
     * @param {array} listenerArr
     */
    const bindAutoPositionEvents = function (el, positioningCallback, windowObj, listenerArr) {
        autoPositionHandler(el, positioningCallback, listenerArr);
        bindWindowResize(el, positioningCallback, windowObj, listenerArr);
    };
    /* below methods help with auto positioning logic */
    /**
     * Returns an array of position classes representing the sides adjacent to a given side
     *
     * @private
     * @function getAdjacentSides
     *
     * @returns {array}  - array of class name strings
     * @param {string} side - side of interest
     */
    const getAdjacentSides = function (side) {
        let adjacentSide = (side === 'top' || side === 'bottom') ? 'left' : 'top';
        return [adjacentSide, OPPOSITE_POSITIONS[adjacentSide]];
    };
    /**
     * Returns an array of positions for a given side
     *
     * @private
     * @function getSidePositions
     *
     * @returns {array}  - array of position strings
     * @param {string} side - side of interest
     * @param {array} alignments  - an array containing alignments (2 values expected)
     */
    const getSidePositions = function (side, alignments) {
        return [side, `${side}-${alignments[0]}`, `${side}-${alignments[1]}`];
    };
    /**
     * Here the order of alternate positions to try is defined.
     * Order logic can be summed up as: current side & alignments (not including current position),
     * opposite side & alignments, and then the 2 other sides and their alignments.
     * Last value is the starting pos in case all alt positions are tried and none work
     *
     * @private
     * @function getAutoOrder
     *
     * @returns {array}  - array of position strings
     * @param {string}  position
     */
    const getAutoOrder = function (position) {
        let primaryPos = position.split('-')[0];
        let firstAxis = [primaryPos, OPPOSITE_POSITIONS[primaryPos]];
        let secondAxis = getAdjacentSides(primaryPos);
        let sideOrder = firstAxis.concat(secondAxis);
        let order = [];
        sideOrder.forEach(side => order.push(...getSidePositions(side, getAdjacentSides(side))));
        order.splice(order.indexOf(position), 1);
        order.push(position);
        return order;
    };
    /**
     * Returns an array of dimension strings representing the dimension associated
     * with a given position
     *
     * @private
     * @function getPositionDimension
     *
     * @returns {array}  - array of dimension strings
     * @param {string} side - position of interest
     */
    const getPositionDimension = function (position) {
        let posDim = (position === 'top' || position === 'bottom') ? 'Height' : 'Width';
        let otherDim = (posDim === 'Height') ? 'Width' : 'Height';
        return [posDim, otherDim];
    };
    /**
     * Given the relevant HTML elements, returns useful element data
     * (for use with getPositionData).
     *
     * @private
     * @function getElementValues
     *
     * @returns {object}  - an object of different relevant element data
     * @param {HTMLElement} tooltipEl - tooltip message element
     * @param {HTMLElement} anchorEl - the element that triggers the tooltip on hover
     */
    const getElementValues = function (tooltipEl, anchorEl) {
        let tooltipSize = tooltipEl.getBoundingClientRect();
        let anchorSize = anchorEl.getBoundingClientRect();
        let tooltipWidthDiff = Math.max(tooltipSize.width - anchorSize.width, 0);
        let tooltipHeightDiff = Math.max(tooltipSize.height - anchorSize.height, 0);
        let halfWidthDiff = Math.round(tooltipWidthDiff / 2);
        let halfHeightDiff = Math.round(tooltipHeightDiff / 2);
        return {
            halfWidthDiff,
            halfHeightDiff,
            tooltipWidth: tooltipSize.width + CARAT_PADDING,
            tooltipHeight: tooltipSize.height + CARAT_PADDING,
            anchorWidth: anchorSize.width,
            anchorHeight: anchorSize.height
        };
    };
    /**
     * Given the relevant HTML element and showIcon config, returns the
     * needed position data and offset data for use with isPosInViewport
     * Uses getElementValues() and position.utils viewportOffset() method.
     *
     * @private
     * @function getPositionData
     *
     * @returns {array}  - an array containing position data and offset data
     * @param {HTMLElement} el  - tooltip component
     * @param {boolean} showIcon
     */
    const getPositionData = function (el, showIcon) {
        let tooltipEl = el.getElementsByClassName('tooltip')[0];
        let anchorEl;
        if (showIcon) {
            anchorEl = el.getElementsByClassName('icon')[0];
        }
        else {
            anchorEl = el.getElementsByClassName('trigger-message')[0];
        }
        let elData = getElementValues(tooltipEl, anchorEl);
        let offsets = positionFactory().viewportOffset(anchorEl, true, true);
        return [elData, offsets];
    };
    /**
     * Calculate the space needed for an element described by elData
     * at the given position. ('dimPos' means 'dimension position')
     *
     * @private
     * @function calcSpaceForPosition
     *
     * @returns {object}  - an object with top, left, right, bottom props
     * @param {string} position
     * @param {object} elData - element widths and height, etc. Generated through getPositionData()
     */
    const calcSpaceForPosition = function (position, elData) {
        let oppositePos = OPPOSITE_POSITIONS[position];
        let [dimPos, dimOther] = getPositionDimension(position);
        let neededSpace = {};
        neededSpace[position] = elData[`tooltip${dimPos}`];
        neededSpace[oppositePos] = 0;
        getAdjacentSides(position).forEach(function (side) {
            neededSpace[side] = elData[`half${dimOther}Diff`];
        });
        return neededSpace;
    };
    /**
     * Calculate the space needed for an element described by elData
     * with the given alignment (assumes spaceObj already contains the
     * results from calcSpaceForPosition to build upon) ('dimPos' means 'dimension position')
     *
     * @private
     * @function calcSpaceForAlignment
     *
     * @returns {object}  - an object with top, left, right, bottom props
     * @param {string} alignment
     * @param {object} elData - element widths and height, etc. Generated through getPositionData()
     * @param {object} spaceObj - an object with top, left, right, bottom props,
     *                            generated through calcSpaceForPosition()
     */
    const calcSpaceForAlignment = function (alignment, elData, spaceObj) {
        let neededSpace = spaceObj;
        let oppositePos = OPPOSITE_POSITIONS[alignment];
        let [dimPos] = getPositionDimension(alignment);
        let deltaWhole = elData[`tooltip${dimPos}`] - ALIGNMENT_MODIFIER[alignment];
        let deltaHalf = Math.round(deltaWhole / 2);
        let anchorHalf = elData[`anchor${dimPos}`] / 2;
        if (neededSpace[oppositePos] > 0) {
            neededSpace[oppositePos] = neededSpace[oppositePos] + deltaHalf;
        }
        else {
            neededSpace[oppositePos] = Math.max(Math.round(deltaWhole - anchorHalf), 0);
        }
        neededSpace[alignment] = Math.max(neededSpace[alignment] - deltaHalf, 0);
        return neededSpace;
    };
    /**
     * Checks if the given element at the given position is in the viewport.
     * Returns true if element is entirely in viewport, false otherwise.
     *
     * @private
     * @function isPosInViewport
     *
     * @returns {boolean}
     * @param {string} position
     * @param {object}  offsets  - the anchor element offsets. Generated with getPositionData()
     * @param {object} elData  - element widths and height, etc. Generated through getPositionData()
     */
    const isPosInViewport = function (position, elData, offsets) {
        let [primaryPos, alignment] = position.split('-');
        let neededSpace = calcSpaceForPosition(primaryPos, elData);
        if (alignment) {
            neededSpace = calcSpaceForAlignment(alignment, elData, neededSpace);
        }
        return Object.keys(neededSpace).every(side => offsets[side] >= neededSpace[side]);
    };
    /**
     * Runs through a position array (generated from getAutoOrder) and tests
     * if an alternate position is in viewport (using isInViewport). Returns a position.
     * NOTE: using a do-while here instead of Array methods because isInViewport
     * does some DOM manip which is a bit expensive so short-circuiting is worthwhile
     *
     * @private
     * @function getVisiblePosition
     *
     * @returns {string}  - representing a visible position
     * @param {HTMLElement} el
     * @param {string}  position
     */
    const getVisiblePosition = function (position, elData, offsets) {
        let newPositionArr = getAutoOrder(position);
        let i = 0;
        let newPosition;
        do {
            newPosition = newPositionArr[i];
            i++;
        } while (i < newPositionArr.length && !isPosInViewport(newPosition, elData, offsets));
        return newPosition;
    };
    /* Public class */
    class easeTooltipController {
        /**
         * Sets the config, does the initial render, and binds the DOM events.
         * (this.listeners is an array that keeps track of current events bound on the element)
         * Errors are console logged--can't use element.html() in case element isn't an Element
         *
         * @public
         * @constructor easeTooltipController
         *
         * @param {HTMLElement}  element  - an html element to render the tooltip in
         * @param {number}  id  - unique id number for component
         * @param {object}  options - options object
         * @param {Window}  windowObj - window object
         */
        constructor(element, tipMessage, id, options, windowObj) {
            this.tooltipWrapper = element;
            try {
                if (checkParamValidity(this.tooltipWrapper, tipMessage, windowObj)) {
                    this.tooltipComp = element[0];
                    this.config = getConfig(tipMessage, id, options, windowObj);
                    this.listeners = [];
                    this.render();
                    this.bindDOMEvents(windowObj);
                }
                else {
                    throw new Error('Incorrect params passed');
                }
            }
            catch (e) {
                this.html = 'easeTooltip encountered an error: ' + e.message;
                console.log(this.html);
            }
        }
        /**
         * Add the mobile/table-only and/or positionAuto behaviors.
         *
         * @public
         * @function bindDOMEvents
         * @memberof easeTooltipController
         *
         * @param {Window} windowObj
         */
        bindDOMEvents(windowObj) {
            if (this.config.notDesktop) {
                let focusCallback = getBoundFocusCallback(this.tooltipComp);
                bindMobileClickout(this.tooltipComp, focusCallback, this.listeners);
            }
            if (this.config.positionAuto) {
                bindAutoPositionEvents(this.tooltipComp, this.autoPosition.bind(this), windowObj, this.listeners);
            }
        }
        /**
         * Checks if the component element is in the viewport. If not--or if
         * forceReposition is true--gets a new position for the tooltip and
         * then renders the tooltip
         *
         * @public
         * @function autoPosition
         * @memberof easeTooltipController
         *
         * @param {boolean} forceReposition - optional
         */
        autoPosition(forceReposition) {
            let [elData, offsets] = getPositionData(this.tooltipComp, this.config.showIcon);
            let position = this.config.cleanedPosition;
            if (forceReposition || !isPosInViewport(position, elData, offsets)) {
                position = getVisiblePosition(position, elData, offsets);
            }
            if (position !== this.config.curPosition) {
                this.config.curPosition = position;
                this.config.positionClasses = getAllPositionClasses(position);
                this.render();
            }
        }
        /**
         * Renders the component
         *
         * @public
         * @function render
         * @memberof easeTooltipController
         */
        render() {
            this.html = mustache.render(partial, this.config);
            this.tooltipWrapper.html(this.html);
        }
    }
    return easeTooltipController;
}());



