import angular from 'angular';
import easeErrorMessageComponent from './easeErrorMessage.component';
/**
 * @ngdoc module
 * @name easeErrorMessage
 * @module easeErrorMessage
 * @description
 * erroMessage component will be used as a placeholder to display error messages in the webpage.
 *
 * @usage
 * ```html
 *<ease-error-message icon-color="'#ff0000'" alert-content="We are not able to show all external accounts at the moment" is-close-icon=true global-holder-id="globalMessageHolder" message-area-id="messageArea" message-close-id="messageClose" message-icon-id="messageIcon"></ease-error-message>
 * ```
 *
 * @param {string} iconColor color of the alert icon.
 * @param {string} alertContent content for the alert message.
 * @param {string} isCloseIcon set to true if component is dismissable, otherwise set to false.
 * @param {string} globalHolderId id for globalMessageHolder.
 * @param {string} messageAreaId id for messageArea.
 * @param {string} messageCloseId id for messageClose.
 * @param {string} messageIconId id for  messageIcon.
 *
 * @example
 * <example name="easeErrorMessage" module="easeErrorMessage">
 *   <file name="index.html">
 *       <script src="script.js"></script>
 *        <h4>easeErrorMessage component with alert icon</h4>
 *        <ease-error-message icon-color="'#ff0000'" alert-content="We are not able to show all external accounts at the moment" is-close-icon=false global-holder-id="globalMessageHolderWithAlert"></ease-error-message>
 *        <h4>easeErrorMessage component with 'X'</h4>
 *        <ease-error-message icon-color="'rgba(255, 255, 255, 0)'" alert-content="We are not able to show all external accounts at the moment" is-close-icon=true global-holder-id="globalMessageHolderWithClose"></ease-error-message>
 *        <h4>easeErrorMessage component with alert icon and 'X'</h4>
 *        <ease-error-message icon-color="'#ff0000'" alert-content="We are not able to show all external accounts at the moment" is-close-icon=true global-holder-id="globalMessageHolderWithCloseAndAlert"></ease-error-message>
 *        <h4>easeErrorMessage component without alert icon and 'X'</h4>
 *        <ease-error-message icon-color="'rgba(255, 255, 255, 0)'" alert-content="We are not able to show all external accounts at the moment" is-close-icon=false global-holder-id="globalMessageHolderWithoutCloseAndAlert"></ease-error-message>
 *   </file>
 *   <file name="script.js">
 *     angular.module('easeErrorExample', ['easeErrorMessage'])
 *    .controller('errorController', ['$scope', function($scope) {
 *       $scope.handleCloseButtonClick = function () {
 *         alert("Close button dummy callback")
 *       }
 *     }])
 *   </file>
 * </example>
 */
export default angular.module('easeErrorMessage', [])
    .component('easeErrorMessage', easeErrorMessageComponent);



