import angular from 'angular';
import CarouselComponent from './easeCarousel.component';
/**
  * @ngdoc module
  * @name easeCarousel
  * @module easeCarousel
  *
  * @description
  * Component that accepts 5 inputs: text to display (label), a method to go to a previous item (togglePrev), a method to go to a next
  * item (toggleNext), a method to check if a previous item exists (showPrev), and a method to check if a next item exists (showNext).
  * This can be used in conjunction with other components (i.e. Month Picker) to update view or items displayed.
  *
  * @usage
  * ```html
  * <ease-carousel label="someDisplayText" toggle-prev="goToPrev()" toggle-next="goToNext()" show-prev="checkForPrev()" show-next="checkForNext()"></ease-carousel>
  * ```
  *
  * @param {object} label Current value to display in carousel (i.e. year, month, etc).
  * @param {function} togglePrev Method to go to the previous item in the carousel.
  * @param {function} toggleNext Method to go to the next item in the carousel.
  * @param {function} showPrev Method returning true/false that checks if previous item exists, determines if previous arrow displays.
  * @param {function} showNext Method returning true/false that checks if next item exists, determines if next arrow displays.
  *
  * @example
  * <example name="carousel" module="carouselExample">
  *   <file name="index.html">
  *     <script src="script.js"></script>
  *     <div ng-controller="CarouselController" style="width: 300px; margin: 0 auto;">
  *       <ease-carousel label="displayLabel" toggle-prev="goToPrev()" toggle-next="goToNext()" have-prev="checkForPrev()" have-next="checkForNext()"></ease-carousel>
  *     </div>
  *   </file>
  *   <file name="script.js">
  *     angular.module('carouselExample', ['easeUIComponents'])
  *       .controller('CarouselController', ['$scope', function($scope) {
  *         $scope.displayLabel = 3
  *         $scope.goToPrev = function () {
  *           $scope.displayLabel = $scope.displayLabel - 1
  *         }
  *         $scope.goToNext = function () {
  *           $scope.displayLabel = $scope.displayLabel + 1
  *         }
  *         $scope.checkForPrev = function () {
  *           if ($scope.displayLabel <= 1) {
  *             return false
  *           }
  *           return true
  *         }
  *         $scope.checkForNext = function () {
  *           if ($scope.displayLabel >= 5) {
  *             return false
  *           }
  *           return true
  *         }
  *       }])
  *   </file>
  * </example>
  *
*/
export default angular.module('easeCarousel', [])
    .component('easeCarousel', CarouselComponent);



