import { PROPERTIES } from 'componentProperties.util';
class easeErrorMessageController {
    constructor() {
        this.closeAlert = true;
        this.alertIconAria = PROPERTIES.errorMessage.icons.alert;
        this.closeIconAria = PROPERTIES.errorMessage.icons.close;
        const genericId = this.globalHolderId;
        Object.assign(this, {
            containerId: `${genericId}Container`,
            alertIconId: `${genericId}Icon`,
            alertTextId: `${genericId}Text`,
            closeIconId: `${genericId}Close`
        });
    }
    handleCloseButtonClick() {
        this.closeAlert = false;
    }
}
export default easeErrorMessageController;



