import angular from 'angular';
import easeStatementPickerComponent from './easeStatementPicker.component';
/**
  * @ngdoc module
  * @name easeStatementPicker
  * @module easeStatementPicker
  *
  * @description
  * The statement picker component contains the month picker, quarter picker, and carousel components, as well as a year end summary
  * button, bundled together into one directive.  The component accepts an object (params) that includes the parameters listed in the
  * Arguments section below.
  *
  * @usage
  * ```html
  * <ease-statement-picker params="paramList"></ease-statement-picker>
  * ```
  *
  * @param {boolean} useMonths Determines if the month picker component will be shown.
  * @param {boolean} useYearly Determines if the yearly summary button will be shown.
  * @param {boolean} useQuarters Determines if the quarter picker component will be shown.
  * @param {boolean} useCarousel Determines if the carousel component will be shown.
  * @param {array} monthStatements Array of monthly statements.
  * @param {array} quarterStatements Array of quarterly statements.
  * @param {object} yearlyStatement Object for year end summary statement.
  * @param {function} haveYearly Function returning a boolean that checks if a year end summary statement exists.
  * @param {object} selectedStatement Variable that is used to track the statement selected by the user.
  * @param {object} year Variable tracking the year corresponding to the statements being displayed.
  * @param {String} yearlyText Optional text to display on the yearly summary button, defaults to Year End Summary if not specified.
  * @param {function} goToPrev Function to load the previous year's statement data.
  * @param {function} goToNext Function to load the next year's statement data.
  * @param {function} havePrev Function returning a boolean that checks for statement data in the previous year.
  * @param {function} haveNext Function returning a boolean that checks for statement data in the next year.
  *
  * @example
  * <example name="statement-picker" module="statementPickerExample">
  *   <file name="index.html">
  *     <script src="script.js"></script>
  *     <div ng-controller="StatementPickerController" style="width: 240px; margin: 0 auto;">
  *       <ease-statement-picker params="paramList"></ease-statement-picker>
  *       <p> Selected statement is {{paramList.selectedStatement}} </p>
  *       <button data-ng-click="toggle('months')"> Click to toggle months on/off </button>
  *       <button data-ng-click="toggle('quarters')"> Click to toggle quarters on/off </button>
  *       <button data-ng-click="toggle('yearly')"> Click to toggle year summary on/off </button>
  *       <button data-ng-click="toggle('carousel')"> Click to toggle carousel on/off </button>
  *     </div>
  *   </file>
  *   <file name="script.js">
  *     angular.module('statementPickerExample', ['easeUIComponents'])
  *       .controller('StatementPickerController', ['$scope', function($scope) {
  *         var months_2014 = [
  *           {month: 'Jan', date: '20', available: false, year: '2014'}, {month: 'Feb', date: '20', available: false, year: '2014'},
  *           {month: 'Mar', date: '20', available: true, year: '2014'}, {month: 'Apr', date: '20', available: true, year: '2014'},
  *           {month: 'May', date: '20', available: true, year: '2014'}, {month: 'Jun', date: '20', available: true, year: '2014'},
  *           {month: 'Jul', date: '20', available: true, year: '2014'}, {month: 'Aug', date: '20', available: true, year: '2014'},
  *           {month: 'Sep', date: '20', available: true, year: '2014'}, {month: 'Oct', date: '20', available: true, year: '2014'},
  *           {month: 'Nov', date: '20', available: true, year: '2014'}, {month: 'Dec', date: '20', available: true, year: '2014'}
  *           ]
  *         var months_2015 = [
  *           {month: 'Jan', date: '19', available: true, year: '2015'}, {month: 'Feb', date: '19', available: true, year: '2015'},
  *           {month: 'Mar', date: '19', available: true, year: '2015'}, {month: 'Apr', date: '19', available: true, year: '2015'},
  *           {month: 'May', date: '19', available: true, year: '2015'}, {month: 'Jun', date: '19', available: true, year: '2015'},
  *           {month: 'Jul', date: '19', available: true, year: '2015'}, {month: 'Aug', date: '19', available: true, year: '2015'},
  *           {month: 'Sep', date: '19', available: true, year: '2015'}, {month: 'Oct', date: '19', available: true, year: '2015'},
  *           {month: 'Nov', date: '19', available: true, year: '2015'}, {month: 'Dec', date: '19', available: true, year: '2015'}
  *           ]
  *         var months_2016 = [
  *           {month: 'Jan', date: '18', available: true, year: '2016'}, {month: 'Feb', date: '18', available: true, year: '2016'},
  *           {month: 'Mar', date: '18', available: true, year: '2016'}, {month: 'Apr', date: '18', available: true, year: '2016'},
  *           {month: 'May', date: '18', available: false, year: '2016'}, {month: 'Jun', date: '18', available: false, year: '2016'},
  *           {month: 'Jul', date: '18', available: false, year: '2016'}, {month: 'Aug', date: '18', available: false, year: '2016'},
  *           {month: 'Sep', date: '18', available: false, year: '2016'}, {month: 'Oct', date: '18', available: false, year: '2016'},
  *           {month: 'Nov', date: '18', available: false, year: '2016'}, {month: 'Dec', date: '18', available: false, year: '2016'}
  *           ]
  *         var quarters_2014 = [
  *           {title: 'Jan-MarQ1 Summary', available: true, year: '2014'}, {title: 'Apr-JunQ2 Summary', available: true, year: '2014'},
  *           {title: 'Jul-SepQ3 Summary', available: true, year: '2014'}, {title: 'Oct-DecQ4 Summary', available: true, year: '2014'}
  *           ]
  *         var quarters_2015 = [
  *           {title: 'Jan-MarQ1 Summary', available: true, year: '2015'}, {title: 'Apr-JunQ2 Summary', available: true, year: '2015'},
  *           {title: 'Jul-SepQ3 Summary', available: true, year: '2015'}, {title: 'Oct-DecQ4 Summary', available: true, year: '2015'}
  *           ]
  *         var quarters_2016 = [
  *           {title: 'Jan-MarQ1 Summary', available: true, year: '2016'}]
  *         var yearly_2014 = {year: 2014, available: true}
  *         var yearly_2015 = {year: 2015, available: true}
  *         var yearly_2016 = {}
  *         function getMonthList(year) {
  *           if (year === 2014) {
  *             return months_2014
  *           } else if (year === 2015) {
  *             return months_2015
  *           } else {
  *             return months_2016
  *           }
  *         }
  *         function getQuarterList(year) {
  *           if (year === 2014) {
  *             return quarters_2014
  *           } else if (year === 2015) {
  *             return quarters_2015
  *           } else {
  *             return quarters_2016
  *           }
  *         }
  *         function getYearly(year) {
  *           if (year === 2014) {
  *             return yearly_2014
  *           } else if (year === 2015) {
  *             return yearly_2015
  *           } else {
  *             return yearly_2016
  *           }
  *         }
  *         $scope.toggle = function(picker) {
  *           if (picker === 'months') {
  *             $scope.paramList.useMonths = !($scope.paramList.useMonths)
  *           } else if (picker === 'quarters') {
  *             $scope.paramList.useQuarters = !($scope.paramList.useQuarters)
  *           } else if (picker === 'yearly') {
  *             $scope.paramList.useYearly = !($scope.paramList.useYearly)
  *           } else {
  *             $scope.paramList.useCarousel = !($scope.paramList.useCarousel)
  *           }
  *         }
  *
  *         $scope.paramList = {
  *           useMonths: true,
  *           useQuarters: true,
  *           useYearly: true,
  *           useCarousel: true,
  *           monthStatements: months_2016,
  *           quarterStatements: quarters_2016,
  *           yearlyStatement: yearly_2016,
  *           haveYearly: function() {
  *             if ($scope.paramList.year === 2016) {
  *               return false
  *             }
  *             return true
  *           },
  *           selectedStatement: {},
  *           year: 2016,
  *           goToPrev: function() {
  *             $scope.paramList.year = $scope.paramList.year - 1
  *             $scope.paramList.monthStatements = getMonthList($scope.paramList.year)
  *             $scope.paramList.quarterStatements = getQuarterList($scope.paramList.year)
  *             $scope.paramList.yearlyStatement = getYearly($scope.paramList.year)
  *           },
  *           goToNext: function() {
  *             $scope.paramList.year = $scope.paramList.year + 1
  *             $scope.paramList.monthStatements = getMonthList($scope.paramList.year)
  *             $scope.paramList.quarterStatements = getQuarterList($scope.paramList.year)
  *             $scope.paramList.yearlyStatement = getYearly($scope.paramList.year)
  *           },
  *           havePrev: function() {
  *             if ($scope.paramList.year <= 2014) {
  *               return false
  *             }
  *             return true
  *           },
  *           haveNext: function() {
  *             if ($scope.paramList.year >= 2016) {
  *               return false
  *             }
  *             return true
  *           }
  *         }
  *       }])
  *   </file>
  * </example>
*/
export default angular.module('easeStatementPicker', [])
    .component('easeStatementPicker', easeStatementPickerComponent);



