import cropPubSub from './easeImageCropPubSub';
import cropHost from './easeImageCropHost.external';
export default class EaseImageCropController {
    /* @ngInject */
    constructor($scope, $element, $timeout, $document) {
        this.scope = $scope;
        this.element = $element;
        this.timeout = $timeout;
        this.storedResultImage = '';
        this.cropA11yMsg = 'To move the cropping area, use the arrow keys. To increase or decrease the size of the cropping area, use the Shift + Arrow keys';
        this.canvasAriaMsg = 'image cropping canvas';
        this.events = cropPubSub();
        this.events.on('area-move-end area-resize-end image-updated', () => this.timeout(() => this.updateResultImage(), 0));
        this.cropHost = cropHost($document)(this.element.find('canvas'), {}, this.events);
        this.scope.$watch('vm.image', () => {
            this.cropHost.setNewImageSource(this.image);
            this.setDimension();
        });
        this.updateResultImage();
        this.scope.$on('$destroy', () => this.cropHost.destroy());
        this.element.on('keydown', this.cropHost.onKeyDown);
        this.element.on('keyup', this.cropHost.onKeyUp);
    }
    updateResultImage() {
        let resultImage = this.cropHost.getResultImageDataURI();
        if (this.storedResultImage !== resultImage) {
            this.storedResultImage = resultImage;
            this.resultImage = resultImage;
        }
    }
    setDimension() {
        let width = this.element[0].clientWidth;
        let height = this.element[0].clientHeight;
        this.cropHost.setMaxDimensions(width, height);
    }
}



