import templateUrl from './easeCarousel.html';
import controller from './easeCarousel.controller';
import 'ease-carousel.scss';
let CarouselComponent = {
    restrict: 'E',
    bindings: {
        label: '=',
        togglePrev: '&',
        toggleNext: '&',
        havePrev: '&',
        haveNext: '&'
    },
    templateUrl,
    controller,
    controllerAs: 'vm'
};
export default CarouselComponent;



