import * as angular from 'angular';
import { KEY } from '../../utilities/keyCode.util';
const { SPACE, ENTER } = KEY;
require('custom-event-polyfill/custom-event-polyfill');
const includes = require('array-includes');
export default (function () {
    return class {
        /**
         * @public
         * @function constructor
         * accepts the DOM node that represents the Drawer
         * @param {Object} DOM Node
         */
        /* @ngInject */
        constructor($element) {
            this.isButton = node => node.nodeName === 'BUTTON';
            this.isLink = node => node.nodeName === 'A';
            this.isSubmit = node => node.nodeName === 'INPUT' && node.type === 'submit';
            this.isOpened = node => node.hasClass('open');
            this.canDrawerToggle = (detail, { drawerId: baseId, drawerContent }) => {
                let { actionType, drawerId } = detail;
                let result = false;
                if (drawerId !== baseId) {
                    return false;
                }
                else {
                    let isDrawerOpen = this.isOpened(drawerContent);
                    switch (actionType.toLowerCase()) {
                        case 'open':
                            result = !isDrawerOpen;
                            break;
                        case 'close':
                            result = isDrawerOpen;
                            break;
                        default:
                            break;
                    }
                    return result;
                }
            };
            this.publishDrawerState = ({ actionType, drawerId }) => {
                document.dispatchEvent(new CustomEvent('drawer-state-change', {
                    detail: { actionType, drawerId }
                }));
            };
            this.drawerContent = $element.find('li');
            this.drawerElement = $element.parent();
            this.isClickEnabled = angular.isUndefined(this.isClickEnabled) ? true : this.isClickEnabled;
            this.drawerElement.addClass('ease-drawer');
            this.drawerElement.on('keypress', function (evt) {
                this.handleKeyPress(evt);
            }.bind(this));
            if (this.isClickEnabled) {
                this.drawerElement.on('click', function ({ target }) {
                    this.handleClick(target);
                }.bind(this));
            }
            // Custom event fired that gets fired by consumer.
            document.addEventListener('toggle-drawer', function ({ detail }) {
                let { actionType, drawerId } = detail;
                this._toggle(this.canDrawerToggle({ actionType, drawerId }, this), this.maxHeight);
            }.bind(this));
        }
        handleKeyPress({ target, keyCode }) {
            if (includes([SPACE, ENTER], keyCode)) {
                this.handleClick(target);
            }
            else {
                return;
            }
        }
        _toggle(action, maxHeight = 2000) {
            if (action) {
                this.drawerContent.toggleClass('open');
                let opened = this.isOpened(this.drawerContent);
                let actionType = opened ? 'open' : 'close';
                this.publishDrawerState({ actionType, drawerId: this.drawerId });
                this.drawerContent.attr('aria-expanded', opened).attr('aria-hidden', !opened);
                this.drawerContent.attr('tabindex', opened ? '0' : '-1');
                maxHeight = !opened ? 0 : maxHeight;
                this.drawerContent.css('max-height', `${maxHeight}px`);
            }
            else {
                return;
            }
        }
        handleClick(target) {
            if (this.isButton(target) || this.isLink(target) || this.isSubmit(target)) {
                return;
            }
            else {
                this._toggle(true, this.maxHeight);
            }
        }
    };
}());



