import { KEY } from 'keyCode.util';
const lockIcon = require('locked.svg');
const primaryIcon = require('star.svg');
let displayIcons = { 'locked': lockIcon, 'primary': primaryIcon };
let iconTexts = { 'locked': 'Locked Icon', 'primary': 'Primary Icon' };
class EaseInputController {
    /* @ngInject */
    constructor($element, $timeout, $scope) {
        this.element = $element;
        this.timeout = $timeout;
        this.scope = $scope;
        this.staticContent = this.staticContent || {};
        this.charLimit = this.charLimit || null;
        this.length = this.length || 24;
        this.inputContent = this.element[0].getElementsByClassName('ease-input-field')[0];
        this.contentLength = this.inputContent.value.length;
        this.hasError = false;
        this.isRequired = this.isRequired || false;
        this.inFocus = false;
        this.inputWidth = '';
        this.timeout(this.findInputWidth.bind(this));
        this.scope.$watch('vm.inputStatus', (newVal) => {
            this.isLocked = newVal === 'locked';
            this.displayIcon = displayIcons[newVal] || '';
            this.iconAltText = iconTexts[newVal] || '';
        });
        this.scope.$watch('vm.errorStatus', (newVal) => {
            this.hasError = newVal === 'error';
            this.errorOutput = this.hasError ? this.errorMsg : '';
        });
    }
    statusIcons() {
        return { 'error': this.hasError, 'locked': this.isLocked };
    }
    findInputWidth() {
        this.inputWidth = this.inputContent.offsetWidth + 'px';
    }
    onInput() {
        let limitLabel = this.element[0].getElementsByClassName('ease-input-sublabel')[0];
        let content = this.inputContent.value;
        this.contentLength = content.length;
        if (limitLabel) {
            limitLabel.innerHTML = this.contentLength + ' / ' + this.charLimit;
        }
    }
    onFocus() {
        this.hasError = false;
        this.inFocus = true;
        this.timeout(() => { });
    }
    onBlur(e) {
        this.checkContent(e);
    }
    onKeydown(e) {
        this.handleKeydown(e);
    }
    handleKeydown(e) {
        this.inputKey = true;
        for (let key in KEY) {
            if (e.which === KEY[key]) {
                this.inputKey = false;
                break;
            }
        }
        if (this.inputKey && this.contentLength === this.charLimit && !this.inputType) {
            this.errorOutput = this.staticContent.maxCharMsg || this.maxCharMsg || 'Reached maximum character limit.';
            this.hasError = true;
        }
        else {
            this.errorOutput = '';
            this.hasError = false;
        }
        if (e.which === KEY.TAB || e.which === KEY.ENTER) {
            this.checkContent(e);
        }
        // update DOM
        this.timeout(() => { });
    }
    checkContent(e) {
        let content = this.inputContent.value;
        this.contentLength = content.length;
        this.validateResult = this.validateInput({ content });
        let hasValidationError = (this.validateResult !== undefined && this.contentLength) ? !this.validateResult : false;
        let hasRequiredFieldError = this.isRequired && !this.contentLength;
        this.hasError = (hasValidationError || hasRequiredFieldError);
        this.inFocus = false;
        this.errorOutput = '';
        if (hasValidationError) {
            this.inFocus = !this.inputType;
            this.errorOutput = this.staticContent.validationMsg || this.validationMsg || 'Validation failed.';
        }
        else if (hasRequiredFieldError) {
            this.inFocus = !this.inputType;
            this.errorOutput = this.requiredMsg || 'This field is required.';
        }
        // update DOM
        this.timeout(() => { });
    }
}
export default EaseInputController;



