import controller from './easeErrorMessage.controller';
import 'ease-error-message.scss';
let easeErrorMessageComponent = {
    restrict: 'E',
    bindings: {
        iconColor: '@',
        alertContent: '@',
        isCloseIcon: '=',
        globalHolderId: '@'
    },
    controller,
    template: `
      <div class="easeErrorGlobalMessageHolder" data-ng-show="vm.closeAlert" id="{{vm.globalHolderId}}" tabindex="-1">
        <div class="easeErrorMessage" id="{{vm.containerId}}" tabindex="0">
          <span class="offscreen" aria-hidden="false">{{vm.alertIconAria}}</span>
          <i class="icon-problem" id="{{vm.alertIconId}}" tabindex="-1" ng-style="{ 'color':{{vm.iconColor}} }" aria-hidden="true"></i>
          <div class="easeErrorMessageArea" id="{{vm.alertTextId}}" tabindex="-1" aria-label=" {{vm.alertContent}}"> 
                  {{vm.alertContent}}
          </div>
        </div>
        <div class="easeErrorMessageClose" data-ng-show="vm.isCloseIcon" id="{{vm.closeIconId}}" data-ng-click="vm.handleCloseButtonClick()" aria-label="{{vm.closeIconAria}}" tabindex="0"></div>
      </div>
`,
    controllerAs: 'vm'
};
export default easeErrorMessageComponent;



