import * as angular from 'angular';
import easeModalService from './easeUIModal.util';
import easeModalComponent from './easeUIModal.component';
/**
  * @ngdoc module
  * @name easeUiModal
  * @module easeUIModal
  *
  * @description
  * Component that, together with easeUIModalService, allows the consumer to create and use a fully functional modal. The modal
  * takes in a method to close the modal (closeModal), an ID for the modal close button (modalId), a string to display as the title in the
  * modal (modalTitle), and optional parameters that determine the role of the modal for accessibility tools (modalRole, default is dialog),
  * if the modal should be double wide (doubleWide, default is false), if the modal should be full screen in desktop view (fullScreen,
  * default is false), and if the modal icon circle should be shown in the mobile view (showIconMobile, default is false). The modal
  * service uses promises for the modal, allowing the consumer to pass data from the modal to the page it was loaded on, as well as
  * pass data into the modal. The steps to set up and use the modal component are as follows:
  *
  * 1.) Add 'easeUIModalService' as a dependency to the controller for the page where the modal will be used.
  *
  * 2.) In the method that will open the modal, use the easeUIModalService.showModal method. Pass an object containing the name of
  * the controller to use in the modal (controller) and the path to the HTML file for the modal (templateUrl) as the parameter for the
  * showModal method. If a controller is not passed in, the modal will use the default controller provided in easeUIModalService, which
  * contains only the close() method. If you need to pass data into the modal, you can include an object with the data defined (inputs).
  * Lastly, you must make sure to include the resolution for the promise (see usage code for reference).
  *
  * 3.) In the controller for the modal, add 'close' as a dependency. You must include $scope.close = close in the controller in order
  * for the modal to close correctly. If data needs to be passed back, it can be included in a result object that is then used
  * as a parameter in the close function (i.e. close($scope.result)). If data has been passed in using the inputs object, you will have
  * to add the name of the input as a dependency in the modal controller in order to reference it. For an example where data is being
  * passed to and from modals, please see the code for the results and confirm modals below.
  *
  * 4.) In the HTML file for the modal, wrap the desired contents of the modal inside the <ease-ui-modal> tag, with the required
  * inputs.
  *
  * @usage
  * ```html
  * <ease-ui-modal close-modal="close()" modal-id="'modalExample'" modal-title="'Your Modal Title'">
  *   Your modal content here
  * </ease-ui-modal>
  * ```
  * ```js
  * easeUIModalService.showModal({
  *   templateUrl: 'path/to/file.html',
  *   controller: controllerForModal
  * }).then(function(modal) {
  *   modal.close.then(function(result) {
  *     // code to handle result object
  *   })
  * })
  * ```
  *
  * @param {function} closeModal Function that triggers when user closes the modal.
  * @param {string} modalId ID value for modal close button. It will be appended to 'closeModal_' (i.e. closeModal_modalExample).
  * @param {string} modalTitle Title to display above the modal content.
  * @param {string} [loopTarget=".ease-modal-close-button"] CSS selector string for override element to be targeted when looping through the modal.
  * @param {string} [modalRole=dialog] Set the role of the modal to use for accessibility tools.
  * @param {boolean} [doubleWide=false] Determines if modal should be shown as double width on desktop view.
  * @param {boolean} [fullScreen=false] Determines if modal should be full screen on desktop view.
  * @param {boolean} [showIconMobile=false] Determines if icon should be shown in mobile view.
  *
  * @example
  * <example name="modal" module="modalExample">
  *   <file name="index.html">
  *     <script src="script.js"></script>
  *     <div data-ng-controller="ExampleController" id="page-content" aria-hidden="false">
  *       <button data-ng-click="openStandardModal()">Open Standard Modal</button><br>
  *       <button data-ng-click="openDoubleWideModal()">Open Double Wide Modal</button><br>
  *       <button data-ng-click="openFullScreenModal()">Open Full Screen Modal</button><br>
  *       <button data-ng-click="openResultsModal()">Open Modal That Returns Results</button>
  *       <div> {{ resultMessage }} </div>
  *     </div>
  *   </file>
  *   <file name="script.js">
  *     angular.module('modalExample', ['easeUIComponents'])
  *       .controller('ExampleController', ['$scope', 'easeUIModalService', function($scope, easeUIModalService) {
  *         $scope.openStandardModal = function() {
  *           easeUIModalService.showModal({
  *             templateUrl: 'standardModal.html',
  *             controller: 'StandardModalController'
  *           }).then(function(modal) {
  *             modal.close.then(function(result) {})
  *           })
  *         }
  *
  *         $scope.openDoubleWideModal = function() {
  *           easeUIModalService.showModal({
  *             templateUrl: 'doubleWideModal.html',
  *             controller: 'DoubleWideModalController'
  *           }).then(function(modal) {
  *             modal.close.then(function(result) {})
  *           })
  *         }
  *
  *         $scope.openFullScreenModal = function() {
  *           easeUIModalService.showModal({
  *             templateUrl: 'fullScreenModal.html',
  *             controller: 'FullScreenModalController'
  *           }).then(function(modal) {
  *             modal.close.then(function(result) {})
  *           })
  *         }
  *
  *         $scope.resultMessage = ''
  *         $scope.openResultsModal = function() {
  *           easeUIModalService.showModal({
  *             templateUrl: 'resultsModal.html',
  *             controller: 'ResultsModalController'
  *           }).then(function(modal) {
  *             modal.close.then(function(result) {
  *               if (result) {
  *                 $scope.openConfirmModal(result)
  *               } else {
  *                 $scope.resultMessage = "You didn't submit a name or age"
  *               }
  *             })
  *           })
  *         }
  *         $scope.openConfirmModal = function(input) {
  *           easeUIModalService.showModal({
  *             templateUrl: 'confirmModal.html',
  *             controller: 'ConfirmModalController',
  *             inputs: {
  *               nameAge: input
  *             }
  *           }).then(function(modal) {
  *             modal.close.then(function(result) {
  *               $scope.resultMessage = "Name: " + result.name + ", Age: " + result.age
  *             })
  *           })
  *         }
  *       }])
  *       .controller('StandardModalController', ['$scope', 'close', function($scope, close) {
  *         $scope.modalMessage = 'Your modal content will appear here.'
  *         $scope.close = close
  *       }])
  *       .controller('DoubleWideModalController', ['$scope', 'close', function($scope, close) {
  *         $scope.modalMessage = 'This is an example of what a desktop double wide modal looks like.'
  *         $scope.close = close
  *       }])
  *       .controller('FullScreenModalController', ['$scope', 'close', function($scope, close) {
  *         $scope.modalMessage = 'This is an example of what a desktop full screen modal looks like.'
  *         $scope.close = close
  *       }])
  *       .controller('ResultsModalController', ['$scope', 'close', function($scope, close) {
  *         $scope.result = {
  *           name: null,
  *           age: null
  *         }
  *         $scope.modalTitle = 'Enter Name and Age'
  *         $scope.close = close
  *         $scope.submit = function () {
  *           close($scope.result)
  *         }
  *       }])
  *       .controller('ConfirmModalController', ['$scope', 'close', 'nameAge', function($scope, close, nameAge) {
  *         $scope.nameAge = nameAge
  *         $scope.close = function () {
  *           close($scope.nameAge)
  *         }
  *       }])
  *
  *   </file>
  *   <file name="standardModal.html">
  *     <ease-ui-modal close-modal="close()" modal-id="'standard'" modal-title="'Lorem ipsum'">
  *       <div style="text-align: center;" tabindex="0" aria-label="{{modalMessage}}"> {{ modalMessage }} </div>
  *     </ease-ui-modal>
  *   </file>
  *   <file name="doubleWideModal.html">
  *     <ease-ui-modal close-modal="close()" modal-id="'doubleWide'" modal-title="'Double Wide Modal'" double-wide="true">
  *       <div tabindex="0" style="text-align: center;" aria-label="{{modalMessage}}"> {{ modalMessage }} </div>
  *     </ease-ui-modal>
  *   </file>
  *   <file name="fullScreenModal.html">
  *     <ease-ui-modal close-modal="close()" modal-id="'fullScreen'" modal-title="'Full Screen Modal'" full-screen="true">
  *       <div tabindex="0" style="text-align: center;" aria-label="{{modalMessage}}"> {{ modalMessage }} </div>
  *     </ease-ui-modal>
  *   </file>
  *   <file name="resultsModal.html">
  *     <ease-ui-modal close-modal="close()" modal-id="'results'" modal-title="modalTitle">
  *       <div style="text-align: center;">
  *         <label for="name">Name</label>
  *         <input style="font-size: medium;" type="text" id="name" placeholder="Enter name" data-ng-model="result.name">
  *       </div>
  *       <div style="text-align: center;">
  *         <label for="age">Age</label>
  *         <input style="font-size: medium;" type="text" id="age" placeholder="Enter age" data-ng-model="result.age">
  *       </div>
  *       <button class="submit" data-ng-click="submit()">Submit</button>
  *     </ease-ui-modal>
  *   </file>
  *   <file name="confirmModal.html">
  *     <ease-ui-modal close-modal="close()" modal-id="'confirm'" modal-title="'Submission Successful'">
  *       <div tabindex="0" style="text-align: center;"> Name: {{ nameAge.name }}, Age: {{ nameAge.age }} </div>
  *     </ease-ui-modal>
  *   </file>
  *   <file name="style.css">
  *     .submit {
  *       height: 50px;
  *       width: 200px;
  *       font-size: 18px;
  *       margin: 25px 0 0 calc(50% - 100px);
  *     }
  *   </file>
  * </example>
*/
export default angular.module('easeUIModal', [])
    .service('easeUIModalService', ['$animate', '$document', '$compile', '$controller', '$rootScope', '$q', '$templateRequest', '$timeout', easeModalService])
    .directive('easeUiModal', easeModalComponent);



