import { PROPERTIES } from '../../utilities/componentProperties.util';
export default class {
    /* @ngInject */
    constructor($element) {
        this.staticContent = this.staticContent || {};
        this.staticContent.placeholder = this.staticContent.placeholder || PROPERTIES.transactionfilter.search.hint;
        this.staticContent.clearImgAlt = this.staticContent.clearImgAlt || PROPERTIES.transactionfilter.search.clearImgAlt;
        this.searchIcon = require('search.svg');
        this.clearIcon = require('search-clear.svg');
        this.element = $element;
        this.searchInput = this.element[0].getElementsByClassName('ease-search-bar')[0];
    }
    clearText() {
        this.searchText = '';
        this.searchInput.focus();
    }
}



