import controller from './easeUiDropdownInput.controller';
import 'ease-ui-dropdownInput.scss';
let easeUiDropdownComponent = {
    restrict: 'E',
    transclude: true,
    replace: true,
    bindings: {
        items: '=?',
        placeHolder: '@',
        isOpen: '=',
        modelValue: '=?',
        defaultOptionIndex: '@',
        disableSearch: '=?',
        filterKey: '@',
        id: '@',
        title: '@',
        subTitle: '@',
        required: '=?',
        errorMessage: '@',
        primaryData: '@',
        secondaryData: '@'
    },
    template: `
    <div class="ease-ui-dropdown-container">
      <label class="ease-ui-dropdown-title" id="ease-dropdown-filter-{{vm.id}}-label">{{vm.title}}</label>
      <span class="ease-ui-dropdown-sub-title" id="ease-dropdown-filter-{{vm.id}}-sublabel" aria-live="polite" aria-atomic="true">{{vm.subTitle}}</span>
      <div class="ease-ui-dropdown-background" data-ng-if = "vm.isOpen" data-ng-click = "vm.background($event)" aria-hidden="true"></div>
      <div class="ease-ui-dropdown" 
           data-ng-click="vm.toggleMenu($event)" 
           data-ng-keydown = "vm.keydown($event)"
           data-ng-focus = "vm.isFocus(false)"
           data-ng-blur = "vm.isFocus(true)"
           tabindex = "0"
           role="navigation"
           >
          <input
            placeholder = {{vm.placeHolder}}
            ng-model = "vm.modelValue"
            data-ng-class="{'input-no-radius-bottom': vm.isOpen}"
            ng-readonly = "{{vm.disableSearch}}"
            id="ease-dropdown-filter-{{vm.id}}"
            aria-label= "{{vm.placeHolder}}"
            data-ng-show="vm.isOpen"
          >
          <div data-ng-hide="vm.isOpen" 
               data-ng-class="{'inputFocus':vm.focus, 'inputFocus-error':vm.required}" 
               class="ease-placeHolder-container" 
               id="ease-dropdown-filter-{{vm.id}}-container" 
               >
            <div data-ng-show = "!vm.togglePlaceHolder">{{vm.oldPlaceHolder}}</div>
            <div data-ng-show = "vm.togglePlaceHolder">
              <span class="primary-data-point" >{{vm.primaryData}}</span>
              <span class="secondary-data-point">{{vm.secondaryData}}</span>
            </div>
          </div>
          <span class="icon-container" role="button" tabindex="0" aria-label="Open drop-down menu">
             <i ng-class = "{'icon-arrow-down': vm.isOpen, 'icon-arrow-up': !vm.isOpen}" aria-hidden="true"><img src="{{vm.arrow}}" alt="Open or Close drop-down"></i>
          </span>
        <div data-ng-transclude></div>
      </div>
    </div>
    <p class="ease-ui-dropdown-required-message" data-ng-class="{'ease-ui-dropdown-required-message': vm.required, 'ease-ui-dropdown-no-required-message': !vm.required}" role="alert" aria-atomic="true">{{vm.message}}</p>`,
    controller,
    controllerAs: 'vm'
};
export default easeUiDropdownComponent;



