import * as angular from 'angular';
import contentTileComponent from './easeContentTile.component';
import transcludeFactory from '../../utilities/multiTransclude.util';
/**
 * @ngdoc module
 * @name easeContentTile
 * @module easeContentTile
 * @restrict E
 * @description
 * This easeContentTile module is used to create each tile for a page with lots of tiles.
 * Each tile is a container with three parts. header-part is optional. When this module is used, users need to always code in the below pattern:
 * inside the &lt;content-tile&gt; tag, there are only one &lt;header-part&gt;, &lt;first-part&gt; and &lt;second-part&gt; tag. &lt;first-part&gt; tag always goes before the &lt;second-part&gt; tag.
 *
 *
 * @usage
 * ```html
 * <ease-content-tile tile-id = "SampleId0" first-id = "sampleId1" second-id = "sanmpleId2">
 *   <header-part>#users' header input#</header-part>
 *   <first-part>#users' first input#</first-part>
 *   <second-part>#users' second input#</second-part>
 * </ease-content-tile>
 * ```
 * @param {string=} tileId Provide id value for the entire content tile
 * @param {string=} headerId Provide id value for the header part of content tile
 * @param {string=} firstId Provide id value for the first part of content tile
 * @param {string=} secondId Provide id value for the second part of content tile
 *
 * @example
 * <example name="easeContentTile" module="easeContentTile">
 *  <file name="index.html">
 *     <ease-content-tile tile-id = "abc" first-id = "123" second-id = "efg-789">
 *       <header-part>
 *          #users' header#
 *       </header-part>
 *       <first-part>
 *         #users' first input#
 *       </first-part>
 *       <second-part>
 *         #users' second input#
 *       </second-part>
 *     </ease-content-tile>
 *   </file>
 * </example>
 *
*/
export default angular.module('easeContentTile', [])
    .directive('easeContentTile', contentTileComponent)
    .factory('transcludeFactory', transcludeFactory);



