import templateUrl from './easeMonthPicker.html';
import controller from './easeMonthPicker.controller';
import 'ease-month-picker.scss';
let MonthPickerComponent = {
    restrict: 'E',
    bindings: {
        months: '=',
        currentMonth: '='
    },
    templateUrl,
    controller,
    controllerAs: 'vm'
};
export default MonthPickerComponent;



