import { KEY } from 'keyCode.util';
import { PROPERTIES } from 'componentProperties.util';
export default (ctrls, scope, element) => {
    ctrls.charLimit = ctrls.charLimit || 50;
    ctrls.staticContent.maxCharMsg = ctrls.staticContent.maxCharMsg || ctrls.maxCharMsg || PROPERTIES.forminputs.email.charlimit1 + ctrls.charLimit + PROPERTIES.forminputs.email.charlimit2;
    ctrls.staticContent.validationMsg = ctrls.staticContent.validationMsg || ctrls.validationMsg || PROPERTIES.forminputs.email.error;
    ctrls.staticContent.placeholder = ctrls.staticContent.placeholder || ctrls.placeholder || PROPERTIES.forminputs.email.hint;
    ctrls.validateInput = (input) => {
        let pattern = /^(?!.*[._@]{2})[a-z0-9][a-z0-9_.]*@\w+(\.\w+)?(\.[a-z]{2,3})?\.[a-z]{2,3}$/i;
        return pattern.test(input.content.trim());
    };
    ctrls.onFocus = () => {
        ctrls.inFocus = false;
    };
    ctrls.onKeydown = (e) => {
        e.which === KEY.SPACE ? e.preventDefault() : null;
        ctrls.handleKeydown(e);
    };
};



