import Controller from './easeAccountDetail.controller';
import 'ease-account-detail.scss';
import 'ease-account-detail-alerts.scss';
import 'ease-account-detail-navigation.scss';
let easeAccountDetailComponent = {
    restrict: 'E',
    bindings: {
        content: '=',
        progressiveAlerts: '=',
        html: '@'
    },
    controller: ['$element', '$compile', '$scope', function (element, $compile, scope) {
            let easeAccountDetailCtrl = new Controller(this.content);
            element.html(easeAccountDetailCtrl.html);
            /*
             * NOTE : how scope is defined for components,
             * forces me to compile with explicitly stated
             * parent scope. This will change if we ever
             * break components into their own repo
            */
            $compile(element.contents())(scope.$parent);
            scope.$watchCollection('vm.progressiveAlerts', (updatedAlerts, oldAlerts) => {
                const newAlertsModel = updatedAlerts === oldAlerts ? oldAlerts : updatedAlerts.filter(alert => oldAlerts.indexOf(alert) < 0);
                let alertsDiv = angular.element(element[0].querySelector('.account-alerts'));
                alertsDiv.append(easeAccountDetailCtrl.getNewAlertHtml(newAlertsModel));
                $compile(element.contents())(scope.$parent);
            });
            return easeAccountDetailCtrl;
        }],
    controllerAs: 'vm'
};
export default easeAccountDetailComponent;



