import * as angular from 'angular';
import easeDrawerComponent from './easeDrawer.component';
/**
 * @ngdoc module
 * @name easeDrawer
 * @module easeDrawer
 * @description
 * This component is used to open/close a section of content.  Click/Tab/Space to open/close the drawer.
 *
 * @usage
 * ```html
 * <ease-drawer drawer-id="'idForTheDrawer'" max-height="150">
 *     Content of the drawer goes here
 * </ease-drawer>
 * ```
 *
 * FAQs
 * How can you track Individual Drawer state?
 * Everytime user opens a individual drawer, drawer-state-change event gets fired with state as actionType and drawerId for the drawer being clicked in detail object.
 *
 * @param {string} drawer-id Provide an id for the drawer content element.
 * @param {number} max-height Optional. Set the maximum height of the drawer.  2000px is the default if it is not set.
 * @param {boolean=} isClickEnabled true if you want click event by default. false if you want to handle click by yourself.
 *
 * @example
 * <example name="easeDrawer" module="easeDrawerExample">
 *   <file name="index.html">
 *     <style>
 *
 *         .ease-drawer {
 *           margin: 10px
 *         }
 *         .ease-drawer-content {
 *           background: #FAF8F9
 *         }
 *     </style>
 *     <div data-ng-controller="EaseDrawerExampleController as ctrl">
 *
 *     <p data-ng-bind="ctrl.message"></p>
 *
 *     <div tabindex="0">
 *        <div>This is the first drawer.  Click/Tab/Space to open the drawer.</div>
 *        <ease-drawer drawer-id="'drawer1'" max-height="150">
 *          <p>max-height of this drawer is set to 150px</p>
 *          <p>I can click on a button inside the drawer</p>
 *          <button id="button1" data-ng-click="ctrl.onClick($event)">Click</button>
 *
 *          <p>I can click on a submit button inside the drawer</p>
 *          <input type="submit" id="submit1" data-ng-click="ctrl.onClick($event)" />
 *        </ease-drawer>
 *     </div>
 *
 *     <div tabindex="0">
 *       <div>This is the second drawer.  It is opened by default.</div>
 *       <ease-drawer drawer-id="'drawer2'">
 *         <p>max-height of this drawer is not set so it is default to 2000px.</p>
 *         <p>I can click on link inside the drawer</p>
 *         <a href="http://www.google.com" target="_blank">Google</a>
 *       </ease-drawer>
 *     </div>
 *
 *     <p>
 *       <input type="button" data-ng-click="ctrl.open()" value="Open Drawer">
 *       <input type="button" data-ng-click="ctrl.close()" value="Close drawer">
 *     </p>
 *     <div tabindex="0">
 *       <div>This is the third drawer.  Click is disabled and can only be opened by keyboard or custom event</div>
 *       <ease-drawer drawer-id="'drawer3'" is-click-enabled = false>
 *         <p>This drawer is opened if ctrl.isOpen() is true</p>
 *       </ease-drawer>
 *     </div>

 *     </div>
 *   </file>
 *
 *   <file name="script.js">
 *     angular.module('easeDrawerExample', ['easeUIComponents'])
 *      .controller('EaseDrawerExampleController', function($scope){
 *       var vm = this
 *       vm.open = function() {
 *         document.dispatchEvent(new CustomEvent('toggle-drawer', {
 *           detail: {
 *             actionType: 'open',
 *             drawerId: 'drawer3'
 *           }
 *         }))
 *       }
 *       vm.close = function () {
 *         document.dispatchEvent(new CustomEvent('toggle-drawer', {
 *          detail: {
 *           actionType: 'close',
 *           drawerId: 'drawer3'
 *          }
 *         }))
 *       }
 *       document.addEventListener('drawer-state-change', function (evt) {
 *         console.log('drawer-state-change is giving me', evt)
 *       })
 *     })
 *    </file>
 *
 * </example>
 */
export default angular.module('easeDrawer', [])
    .component('easeDrawer', easeDrawerComponent);



