import { DEFAULT_MAP_ZOOM_LEVEL, MARKER_URL, STATIC_MAP_BASE_URL, DEFAULT_SIZE, STATIC_MAP_STREET_VIEW_BASE_URL, CLIENT_ID, GoogleMapUtil } from './easeGoogleMap.utils.external';
import mapTemplate from './easeMapTemplate.html';
import staticMapTemplate from './easeStaticMapTemplate.html';
class EaseGoogleMapController {
    /* @ngInject */
    constructor($scope) {
        this.scope = $scope;
        this.templateUrl = this.generateTemplateUrl();
        this.handleMarkerChange($scope);
        this.handlePropertyChanges($scope);
        !this.staticMap ? this.renderMap() : this.renderStaticMap();
    }
    handleMarkerChange($scope) {
        let shouldChangeMarker = (newValue, oldValue) => {
            return !(JSON.stringify(newValue) === JSON.stringify(oldValue));
        };
        $scope.$watchCollection(() => this.markerConfig, (newValue, oldValue) => {
            if ((newValue && oldValue) &&
                shouldChangeMarker(newValue, oldValue)) {
                this.markerConfig = newValue;
                !this.staticMap ? this.renderMap() : this.renderStaticMap();
            }
        });
    }
    handlePropertyChanges($scope) {
        $scope.$watchCollection(() => [this.isStreetView, this.showUiControls, this.zoom,
            this.staticMap, this.size, this.lang], (newValue, oldValue) => {
            if (newValue && oldValue && (newValue.toString() !== oldValue.toString())) {
                !this.staticMap ? this.renderMap() : this.renderStaticMap();
            }
        });
    }
    populateConfig() {
        let zoomUpTo = this.zoom || DEFAULT_MAP_ZOOM_LEVEL;
        let map = (this.isStreetView === true) ? this.getStreetViewConfig(zoomUpTo) : this.getMapViewConfig(zoomUpTo);
        if (!this.isStreetView) {
            this.getMarkersForMap(map);
        }
    }
    renderMap() {
        GoogleMapUtil({ v: 3.24, language: this.lang }).then(() => {
            this.populateConfig();
        }).catch((err) => {
            console.error('Error in getting google map scripts ', err);
        });
    }
    renderStaticMap() {
        let baseURL = !this.isStreetView ? STATIC_MAP_BASE_URL : STATIC_MAP_STREET_VIEW_BASE_URL;
        let markerConfig = (this.markerConfig === undefined)
            ? [{ lat: this.lat, long: this.long }] : this.markerConfig;
        let mapSize = !this.size ? DEFAULT_SIZE : this.size;
        let markers = this.generateMarkersForStaticMap(markerConfig);
        this.staticMapUrl = `${baseURL}${markers}&zoom=${this.zoom}&size=${mapSize}&clientId=${CLIENT_ID}`;
    }
    mapHandler() {
        if (this.clickHandler && typeof this.clickHandler === 'function') {
            this.clickHandler();
        }
    }
    getStreetViewConfig(zoomUpTo) {
        let map = new google.maps.StreetViewPanorama(document.getElementById(this.id), {
            position: { lat: this.lat, lng: this.long },
            pov: { heading: 165, pitch: 0 },
            disableDefaultUI: false || !this.showUiControls,
            zoom: zoomUpTo
        });
        map.addListener('click', this.mapHandler.bind(this));
        google.maps.event.addDomListener(window, 'resize', this.resizeMap.bind(this, map));
        return map;
    }
    getMapViewConfig(zoomUpTo) {
        var mapFocusArea = new google.maps.LatLng(this.lat, this.long);
        let map = new google.maps.Map(document.getElementById(this.id), {
            center: mapFocusArea,
            disableDefaultUI: false || !this.showUiControls,
            zoom: zoomUpTo
        });
        map.addListener('click', this.mapHandler.bind(this));
        google.maps.event.addDomListener(window, 'resize', this.resizeMap.bind(this, map));
        return map;
    }
    resizeMap(map) {
        setTimeout(function () {
            let center = map.getCenter();
            google.maps.event.trigger(map, 'resize');
            map.setCenter(center);
        }, 300);
    }
    generateMarkersForStaticMap(markerConfig) {
        let markerString;
        let getMarkerUrlForIcon = (marker, isFirstTimeRendering = true) => {
            const prefix = isFirstTimeRendering ? '' : '&';
            return !marker['markerIcon'] ? `${prefix}markers=${marker.lat},${marker.long}`.trim() : `${prefix}markers=icon:${marker.markerIcon.url}|${marker.lat},${marker.long}`.trim();
        };
        if (markerConfig) {
            for (let i = 0; i < markerConfig.length; i++) {
                let marker = markerConfig[i];
                if (markerString === undefined) {
                    markerString = getMarkerUrlForIcon(marker);
                }
                else {
                    markerString += getMarkerUrlForIcon(marker, false);
                }
            }
        }
        return markerString;
    }
    getMarkersForMap(map) {
        if (this.markerConfig && Array.isArray(this.markerConfig)) {
            for (let i = 0; i < this.markerConfig.length; i++) {
                let markerConfig = this.markerConfig[i];
                let icon = { url: markerConfig.markerIcon ? markerConfig.markerIcon.url : MARKER_URL };
                if (markerConfig.markerIcon && markerConfig.markerIcon.x && markerConfig.markerIcon.y) {
                    icon.scaledSize = new google.maps.Size(markerConfig.markerIcon.x, markerConfig.markerIcon.y);
                }
                let marker = new google.maps.Marker({
                    position: {
                        lat: markerConfig.lat,
                        lng: markerConfig.long
                    },
                    icon,
                    map,
                    id: markerConfig.id || `marker__${Math.random()}`
                });
                if (this.renderedMarkers) {
                    this.renderedMarkers.push(marker);
                }
                this.attachMarkerMessagesAndEvents(marker, markerConfig.markerText, markerConfig.eventNames, markerConfig.eventHandlers);
            }
        }
        else {
            return new google.maps.Marker({
                map,
                icon: MARKER_URL,
                draggable: true,
                animation: google.maps.Animation.DROP,
                position: { lat: this.lat, lng: this.long }
            });
        }
    }
    attachMarkerMessagesAndEvents(marker, markerMessage, eventNames, eventHandlers) {
        let infowindow = new google.maps.InfoWindow({
            content: markerMessage
        });
        let shouldAttachEvents = (Array.isArray(eventNames) && Array.isArray(eventHandlers)) &&
            (eventNames.length === eventHandlers.length);
        let shouldAttachSingleListener = typeof eventNames === 'string';
        if (shouldAttachEvents) {
            for (let i = 0; i < eventNames.length; i++) {
                let eventHandler = eventHandlers[i];
                let eventName = eventNames[i];
                marker.addListener(eventName, () => {
                    if (eventHandler) {
                        eventHandler.apply(this, [marker, infowindow]);
                    }
                });
            }
        }
        else if (shouldAttachSingleListener) {
            marker.addListener(eventNames, () => {
                if (eventHandlers) {
                    eventHandlers.apply(this, [marker, infowindow]);
                }
            });
        }
    }
    generateTemplateUrl() {
        this.id = `map__${Math.random()}`;
        return this.staticMap ? staticMapTemplate : mapTemplate;
    }
}
export default EaseGoogleMapController;



